// PayPalWeb.js
import React from 'react';

const PayPalWeb = ({ visible, paypalUrl, onNavigationStateChange, onRequestClose }) => {
  const paypalWindowRef = React.useRef(null);
  const checkIntervalRef = React.useRef(null);
  const hasOpenedWindow = React.useRef(false);

  React.useEffect(() => {
    // Only open the window if it hasn't been opened yet for this payment attempt
    if (visible && paypalUrl && !hasOpenedWindow.current) {
      hasOpenedWindow.current = true;
      
      // Open PayPal in new window
      paypalWindowRef.current = window.open(paypalUrl, '_blank');

      checkIntervalRef.current = setInterval(() => {
        try {
          if (paypalWindowRef.current?.closed) {
            cleanupAndClose();
          } else if (paypalWindowRef.current?.location?.href) {
            const currentUrl = paypalWindowRef.current.location.href;
            
            if (currentUrl.includes('payment_success')) {
              handleSuccess();
            } else if (currentUrl.includes('payment_cancel')) {
              handleCancel();
            }
          }
        } catch (e) {
          // Cross-origin errors are expected
        }
      }, 1000);
    }

    return () => cleanupAndClose();
  }, [visible, paypalUrl]);

  const handleSuccess = () => {
    if (checkIntervalRef.current) {
      clearInterval(checkIntervalRef.current);
      checkIntervalRef.current = null;
    }
    
    setTimeout(() => {
      if (paypalWindowRef.current) {
        paypalWindowRef.current.close();
        paypalWindowRef.current = null;
      }
      hasOpenedWindow.current = false;
      onNavigationStateChange({ url: 'payment_success' });
    }, 2000);
  };

  const handleCancel = () => {
    cleanupAndClose();
    onNavigationStateChange({ url: 'payment_cancel' });
  };

  const cleanupAndClose = () => {
    if (checkIntervalRef.current) {
      clearInterval(checkIntervalRef.current);
      checkIntervalRef.current = null;
    }
    
    if (paypalWindowRef.current && !paypalWindowRef.current.closed) {
      paypalWindowRef.current.close();
      paypalWindowRef.current = null;
    }
    
    hasOpenedWindow.current = false;
  };

  return null;
};

export default PayPalWeb;