import React from 'react';
import { View, Text, TouchableOpacity, StyleSheet } from 'react-native';

const NextArrowButton = ({ onPress, isActive }) => {
  return (
    <TouchableOpacity onPress={onPress} activeOpacity={0.7}>
      <View style={[styles.button, isActive && styles.buttonActive]}>
        <Text style={[styles.text, isActive && styles.textActive]}>NEXT</Text>
        <View style={[styles.arrow, isActive && styles.arrowActive]} />
      </View>
    </TouchableOpacity>
  );
};

const styles = StyleSheet.create({
  button: {
    flexDirection: 'row',
    alignItems: 'center',
    borderWidth: 2,
    borderColor: '#4a4a4a',
    backgroundColor: 'white',
    paddingVertical: 10,
    paddingHorizontal: 20,
    borderRadius: 20,
    paddingRight: 40, // Space for arrow
    // iOS shadow properties
    shadowColor: '#000',
    shadowOffset: {
        width: 5,
        height: 8,
    },
    shadowOpacity: 0.25,
    shadowRadius: 3.84,
    // Android shadow
    elevation: 5,
  },
  text: {
    color: 'black',
    fontWeight: 'bold',
    fontSize: 16,
  },
  arrow: {
    width: 0,
    height: 0,
    marginLeft: 10,
    borderTopWidth: 10,
    borderBottomWidth: 10,
    borderLeftWidth: 15,
    borderTopColor: 'transparent',
    borderBottomColor: 'transparent',
    borderLeftColor: 'black',
  },
  buttonActive: {
    backgroundColor: '#4a4a4a',
  },
  textActive: {
    color: 'white',
  },
  arrowActive: {
    borderLeftColor: 'white',
  },
});

export default NextArrowButton;
