import React, { useRef, useEffect } from 'react';
import { View, Text, StyleSheet, SafeAreaView, Image, ScrollView, TouchableOpacity, Platform, Dimensions, Animated } from 'react-native';
import { useNavigation, useRoute, useFocusEffect } from '@react-navigation/native';
import ScrollableGradientWrapper from '../components/ScrollableGradientWrapper';

const { width: SCREEN_WIDTH } = Dimensions.get('window');

// Helper Components
const LabelItem = ({ children }) => (
  <View style={styles.labelItem}>
    <Text style={styles.labelText}>{children}</Text>
  </View>
);

const BorderedItem = ({ children, price, style, isCutLengths }) => (
  <View style={[styles.borderedItem, style]}>
    {isCutLengths ? (
      <View style={styles.cutLengthsLayout}>
        <View style={styles.cutLengthsContent}>
          {children}
        </View>
        {price && (
          <View style={styles.cutLengthsPriceBorder}>
            <Text style={styles.priceText}>
              {typeof price === 'number' ? `A$${price.toFixed(2)}` : price}
            </Text>
          </View>
        )}
      </View>
    ) : (
      <>
        <Text style={styles.itemText}>{children}</Text>
        {price && (
          <View style={styles.priceBorder}>
            <Text style={styles.priceText}>
              {typeof price === 'number' ? `A$${price.toFixed(2)}` : price}
            </Text>
          </View>
        )}
      </>
    )}
  </View>
);

const NumberedCircle = ({ number }) => (
  <View style={styles.numberedCircle}>
    <Text style={styles.numberedCircleText}>{number}</Text>
  </View>
);

const TotalPriceDisplay = ({ total }) => (
  <View style={styles.totalPriceContainer}>
    <View style={styles.totalPriceLabelItem}>
      <Text style={styles.totalPriceLabelText}>TOTAL PRICE</Text>
    </View>
    <View style={styles.totalPriceValueContainer}>
      <Text style={styles.totalPriceValueText}>
        A${total ? total.toFixed(2) : '0.00'}
      </Text>
    </View>
  </View>
);

// Helper Functions
const cleanPriceString = (priceStr) => {
  if (!priceStr) return 0;
  return parseFloat(priceStr.replace(/\s+/g, '').split('/')[0]);
};

// Main Component
const OrderConfirmationScreen1 = () => {
  const navigation = useNavigation();
  const route = useRoute();
  const [isSelected, setIsSelected] = React.useState(false);
  const fadeAnim = useRef(new Animated.Value(0)).current;

  const { 
    selectedHose,
    selectedHosePrice,
    selectedFitting,
    end1Shape,
    end1Size,
    end1Price,
    selectedEnd2Fitting,
    end2Shape,
    end2Size,
    end2Price,
    selectedAngle,
    quantity,
    cutLengths,
    cutLengthPrice,
    selectedProtection,
    selectedPressure,
    selectedMethod,
    selectedService,
    basePrice,
    hoseProtectionPrice,
    pressureTestPrice,
    shippingCost,
    totalBeforeShipping,
    totalPriceWithShipping,
    protectionCost,
    isOrderFittingMode
  } = route.params || {};

  // Helper function for getting total length
  const getTotalLength = () => {
    if (!cutLengths) return 0;
    return cutLengths.reduce((total, cut) => {
      if (!cut.length) return total;
      return total + (parseFloat(cut.length) / 1000); // Convert mm to meters
    }, 0);
  };

  useEffect(() => {
    Animated.timing(fadeAnim, {
      toValue: 1,
      duration: 800,
      useNativeDriver: Platform.OS === 'native',
    }).start();
  }, []);

  useFocusEffect(
    React.useCallback(() => {
      setIsSelected(null);
      fadeAnim.setValue(0);
      Animated.timing(fadeAnim, {
        toValue: 1,
        duration: 800,
        useNativeDriver: Platform.OS === 'native',
      }).start();
    }, [fadeAnim])
  );

  const orderDetails = {
    hoseSize: selectedHose ? selectedHose.size : null,
    end1: end1Shape && end1Size ? { type: end1Shape, size: end1Size } : null,
    end2: end2Shape && end2Size ? { 
      type: end2Shape, 
      size: end2Size,
    } : null,
    angle: selectedAngle || null,
    qty: quantity || null,
    cutLengths: cutLengths && cutLengths.length > 0 ? cutLengths : null,
    hoseProtection: selectedProtection || null,
    pressureTest: selectedPressure || null,
    shippingMethod: selectedMethod || null,
    shippingService: selectedService || null
  };

  // Calculate total price components
  const calculatePrices = () => {
    const totalLength = getTotalLength();
    const baseHosePrice = totalLength * cleanPriceString(selectedHosePrice);
    const protectionAmount = selectedProtection === 'NOT REQUIRED' ? 0 : protectionCost;
    const finalShippingCost = shippingCost || 0;
    const total = baseHosePrice + protectionAmount + finalShippingCost;

    return {
      baseHosePrice,
      protectionAmount,
      finalShippingCost,
      total
    };
  };

  const renderPriceSummary = () => {
    return (
      <View style={styles.priceSummaryContainer}>
        {/* Empty container kept for potential future use */}
      </View>
    );
  };

  const renderOrderDetails = () => {
    const details = Object.entries(orderDetails).filter(([_, value]) => value !== null);
    return details.map(([key, value], index) => {
      let label, content, price;

      switch (key) {
        case 'hoseSize':
          label = 'HOSE SIZE';
          content = value;
          return (
            <View key={index} style={styles.rowContainer}>
              <LabelItem>{label}</LabelItem>
              <BorderedItem>
                {content}
              </BorderedItem>
            </View>
          );
        
        case 'end1':
          label = 'END1';
          content = `${value.type} - ${value.size}`;
          price = end1Price;
          return (
            <View key={index} style={styles.rowContainer}>
              <LabelItem>{label}</LabelItem>
              <BorderedItem price={price}>
                {content}
              </BorderedItem>
            </View>
          );

        case 'end2':
          label = 'END2';
          content = `${value.type} - ${value.size}`;
          price = end2Price;
          return (
            <View key={index} style={styles.rowContainer}>
              <LabelItem>{label}</LabelItem>
              <BorderedItem price={price}>
                {content}
              </BorderedItem>
            </View>
          );

        case 'angle':
          label = 'ANGLE';
          content = value;
          return (
            <View key={index} style={styles.rowContainer}>
              <LabelItem>{label}</LabelItem>
              <BorderedItem>
                {content}
              </BorderedItem>
            </View>
          );

        case 'qty':
          label = 'QTY';
          content = value;
          return (
            <View key={index} style={styles.rowContainer}>
              <LabelItem>{label}</LabelItem>
              <BorderedItem>
                {content}
              </BorderedItem>
            </View>
          );
        
        case 'cutLengths':
          const totalLength = getTotalLength();
          const cutLengthPrice = totalLength * cleanPriceString(selectedHosePrice);
          return (
            <View key={index} style={styles.rowContainer}>
              <LabelItem>CUT LENGTHS</LabelItem>
              <BorderedItem 
                price={cutLengthPrice}
                isCutLengths={true}
                style={styles.cutLengthsContainer}
              >
                <View>
                  {value.map((cut, i) => (
                    <View key={i} style={styles.cutLengthRow}>
                      <NumberedCircle number={i + 1} />
                      <Text style={styles.cutLengthText}>{`${cut.length} ${cut.unit}`}</Text>
                    </View>
                  ))}
                </View>
              </BorderedItem>
            </View>
          );
        
        case 'hoseProtection':
          label = 'HOSE PROTECTION';
          content = value;
          price = value === 'NOT REQUIRED' ? 'NOT REQUIRED' : protectionCost;
          return (
            <View key={index} style={styles.rowContainer}>
              <LabelItem>{label}</LabelItem>
              <BorderedItem price={price}>
                {content}
              </BorderedItem>
            </View>
          );

        case 'pressureTest':
          label = 'PRESSURE TEST';
          content = value;
          price = value === 'Not Required' ? 'NOT REQUIRED' : pressureTestPrice;
          return (
            <View key={index} style={styles.rowContainer}>
              <LabelItem>{label}</LabelItem>
              <BorderedItem price={price}>
                {content}
              </BorderedItem>
            </View>
          );
        
        case 'shippingService':
          label = 'SHIPPING SERVICE';
          content = value;
          price = shippingCost > 0 ? shippingCost : 'FREE';
          return (
            <View key={index} style={styles.rowContainer}>
              <LabelItem>{label}</LabelItem>
              <BorderedItem price={price}>
                {content}
              </BorderedItem>
            </View>
          );

        default:
          label = key.toUpperCase().replace('_', ' ');
          content = typeof value === 'object' ? 
            `${value.type} - ${value.size}` : value;
          return (
            <View key={index} style={styles.rowContainer}>
              <LabelItem>{label}</LabelItem>
              <BorderedItem>
                {content}
              </BorderedItem>
            </View>
          );
      }
    });
  };
  
  const handleShippingDetails = () => {
    setIsSelected(true);
    Animated.timing(fadeAnim, {
      toValue: 0,
      duration: 800,
      useNativeDriver: Platform.OS === 'native',
    }).start(() => {
      navigation.navigate('ShippingDetails', {
        selectedHose,
        selectedHosePrice,
        selectedFitting,
        end1Shape,
        end1Size,
        end1Price,
        selectedEnd2Fitting,
        end2Shape,
        end2Size,
        end2Price,
        selectedAngle,
        quantity,
        cutLengths,
        cutLengthPrice,isOrderFittingMode,
        selectedProtection,
        selectedPressure,
        selectedMethod,
        selectedService,
        basePrice,
        hoseProtectionPrice,
        pressureTestPrice,
        protectionCost,
        shippingCost,
        totalBeforeShipping,
        totalPriceWithShipping: calculatePrices().total,
      });
    });
  };

  useEffect(() => {
    console.log('Debug prices:', {
      selectedHosePrice,
      cutLengths,
      basePrice,
      protectionCost,
      shippingCost,
      totalPriceWithShipping
    });
  }, []);

  const displayTotalPrice = isOrderFittingMode ? cutLengthPrice : totalPriceWithShipping;

  return (
    <SafeAreaView style={styles.mainContainer}>
      {/* Floating Back Button */}
      <TouchableOpacity 
        style={styles.floatingBackButton}
        onPress={() => navigation.goBack()}
      >
        <Text style={styles.backButtonText}></Text>
      </TouchableOpacity>

      <View style={styles.mainContentContainer}>
        <ScrollableGradientWrapper>
          <View style={styles.container}>
            <ScrollView contentContainerStyle={styles.scrollContent}>
              <View style={styles.logoContainer}>
                <Image
                  source={require('../assets/fluidpower_logo.gif')}
                  style={styles.logo}
                  resizeMode="contain"
                />
              </View>
              <Animated.View style={[styles.animatedContainer, { opacity: fadeAnim }]}>
                <View style={styles.titleContainer}>
                  <Text style={styles.titleText}>ORDER Summary</Text>
                </View>
                <View style={styles.orderDetailsContainer}>
                  {renderOrderDetails()}
                </View>
                {renderPriceSummary()}
                <TotalPriceDisplay total={displayTotalPrice} />
                <TouchableOpacity 
                  style={[styles.confirmButton, isSelected && { backgroundColor: '#ffc100' }]} 
                  onPress={handleShippingDetails}
                >
                  <Text style={styles.confirmButtonText}>SHIPPING Details</Text>
                </TouchableOpacity>
              </Animated.View>
            </ScrollView>
          </View>
        </ScrollableGradientWrapper>
      </View>
    </SafeAreaView>
  );
};

