import React, { useRef, useEffect, useState } from 'react';
import { View, Text, StyleSheet, Image, TouchableOpacity, Animated, SafeAreaView, Modal, Platform } from 'react-native';
import { useFocusEffect } from '@react-navigation/native';
import ScrollableGradientWrapper from '../components/ScrollableGradientWrapper';
import { Linking } from 'react-native';

const WelcomeScreen = ({ navigation }) => {
  const fadeAnim = useRef(new Animated.Value(1)).current;
  const [showSelectionModal, setShowSelectionModal] = useState(false);
  const modalFadeAnim = useRef(new Animated.Value(0)).current;
  const [selectedButton, setSelectedButton] = useState(null);

  const handleStartSelection = () => {
    setShowSelectionModal(true);
    Animated.timing(modalFadeAnim, {
      toValue: 1,
      duration: 800,
      useNativeDriver: Platform.OS === 'native',
    }).start();
  };

  const handleBuildHose = () => {
    setSelectedButton('build');
    Animated.timing(modalFadeAnim, {
      toValue: 0,
      duration: 500,
      useNativeDriver: Platform.OS === 'native',
    }).start(() => {
      setShowSelectionModal(false);
      setSelectedButton(null);
      navigation.navigate('HoseSelection', { isHoseOnly: false });
    });
  };
  
  const handleHoseOnly = () => {
    setSelectedButton('hoseOnly');
    Animated.timing(modalFadeAnim, {
      toValue: 0,
      duration: 500,
      useNativeDriver: Platform.OS === 'native',
    }).start(() => {
      setShowSelectionModal(false);
      setSelectedButton(null);
      navigation.navigate('HoseSelection', { isHoseOnly: true });
    });
  };
  
  const handleOrderFitting = () => {
    setSelectedButton('order');
    Animated.timing(modalFadeAnim, {
      toValue: 0,
      duration: 800,
      useNativeDriver: Platform.OS === 'native',
    }).start(() => {
      setShowSelectionModal(false);
      setSelectedButton(null); // Reset selection
      navigation.navigate('End1Fitting', {
        selectedHose: null,
        selectedHosePrice: null,
        isOrderFittingMode: true 
       });
    });
  };

  const handleCloseModal = () => {
    Animated.timing(modalFadeAnim, {
      toValue: 0,
      duration: 500,
      useNativeDriver: Platform.OS === 'native',
    }).start(() => {
      setShowSelectionModal(false);
      setSelectedButton(null);
    });
  };

  const fadeIn = () => {
    fadeAnim.setValue(0);
    Animated.timing(fadeAnim, {
      toValue: 1,
      duration: 800,
      useNativeDriver: Platform.OS === 'native',
    }).start();
  };

  useFocusEffect(
    React.useCallback(() => {
      fadeIn();
      return () => {
        // Optional cleanup if needed
      };
    }, [])
  );

  const handlePhonePress = () => {
    Linking.openURL('tel:+61409517333');
  };
  
  const handleEmailPress = () => {
    Linking.openURL('mailto:info@fluidpowergroup.com.au');
  };

  return (
    <View style={styles.mainContainer}>
      <ScrollableGradientWrapper>
        <SafeAreaView style={styles.container}>
          <View style={styles.logoContainer}>
            <Image
          source={require('../assets/fluidpower_logo.gif')}
          style={styles.logo}
          resizeMode="contain"
        />
      </View>
      <Animated.View style={[styles.contentContainer, { opacity: fadeAnim }]}>
        <View style={styles.titleContainer}>
          <Text style={styles.title}>Hydraulic Hose Builder</Text>
        </View>

        <View style={styles.messageContainer}>
          <Text style={styles.messageText}>
            You are just a <Text style={styles.highlightText}>FEW</Text> steps away from
          </Text>
          <Text style={styles.messageText}>
            <Text style={styles.highlightText}>CREATING</Text> your <Text style={styles.highlightText}>OWN HYDRAULIC HOSE</Text>
          </Text>
        </View>

        <View style={styles.imageContainer}>
          <Image
            source={require('../assets/welcome_hose.png')}
            style={styles.hoseImage}
            resizeMode="contain"
          />
        </View>

        <View style={styles.instructionsContainer}>
          <Text style={styles.instructionsText}>
            Select the Hose Size, End Fittings,
          </Text>
          <Text style={styles.instructionsText}>
            Cut Length, and Shipping Method.
          </Text>
          
          <Text style={[styles.instructionsText, styles.spacedText]}>
            We will crimp and ship it the <Text style={styles.highlightText}>SAME DAY</Text>*.
          </Text>
          
          <Text style={[styles.instructionsText, styles.spacedText]}>
            You're all set!
          </Text>
          
          <Text style={[styles.instructionsText, styles.spacedText]}>
            Let's get started.
          </Text>
        </View>

        <TouchableOpacity
          style={styles.startButton}
          onPress={handleStartSelection}
        >
          <Text style={styles.startButtonText}>START SELECTION</Text>
        </TouchableOpacity>

        <View style={styles.asteriskContainer}>
          <Text style={styles.asteriskText}>
            *<Text style={styles.highlightText}>CUT OFF</Text> time for{' '}
            <Text style={styles.highlightText}>SAME DAY</Text> shipping is{' '}
            <Text style={styles.highlightText}>1530</Text> hrs AEST.
          </Text>
          
          <Text style={styles.enquiryText}>
            For any and all enquiries:
          </Text>
          
          <TouchableOpacity onPress={handlePhonePress}>
            <Text style={styles.contactText}>
              +61 409 517 333
            </Text>
          </TouchableOpacity>
          
          <TouchableOpacity onPress={handleEmailPress}>
            <Text style={styles.contactText}>
              email: info@fluidpowergroup.com.au
            </Text>
          </TouchableOpacity>

          <Text style={styles.copyrightText}>
            FluidPower Group All Rights Reserved {new Date().getFullYear()}
          </Text>
        </View>
        </Animated.View>
      </SafeAreaView>
    </ScrollableGradientWrapper>
              <Modal
            transparent={true}
            visible={showSelectionModal}
            onRequestClose={handleCloseModal}
          >
            <View style={styles.modalOverlay}>
              <TouchableOpacity 
                style={styles.overlayBackground}
                activeOpacity={1}
                onPress={handleCloseModal}
              />
              <Animated.View style={[styles.selectionContainer, { opacity: modalFadeAnim }]}>
                <Text style={styles.selectionTitle}>You are here to</Text>
                
                <TouchableOpacity
                  style={[
                    styles.selectionButton,
                    selectedButton === 'hoseOnly' && styles.selectedButton
                  ]}
                  onPress={handleHoseOnly}
                >
                  <Text style={styles.selectionButtonText}>Order Hose Only</Text>
                </TouchableOpacity>

                <TouchableOpacity
                  style={[
                    styles.selectionButton,
                    selectedButton === 'order' && styles.selectedButton
                  ]}
                  onPress={handleOrderFitting}
                >
                  <Text style={styles.selectionButtonText}>Order Fitting Only</Text>
                </TouchableOpacity>

                <TouchableOpacity
                  style={[
                    styles.selectionButton,
                    selectedButton === 'build' && styles.selectedButton
                  ]}
                  onPress={handleBuildHose}
                >
                  <Text style={styles.selectionButtonText}>Build Complete Hose</Text>
                </TouchableOpacity>

              </Animated.View>
            </View>
        </Modal>
    </View>
  );
};

const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: '#fff',
  },
  mainContainer: {
    flex: 1,
    position: 'relative',
  },
  contentContainer: {
    flex: 1,
    alignItems: 'center',
    paddingBottom: 30,
  },
  logoContainer: {
    alignItems: 'center',
    justifyContent: 'center',
    marginVertical: 20,
    paddingBottom: 15,
    width: '100%',
  },
  logo: {
    width: 400,
    height: 200,
    resizeMode: 'contain',
  },
  titleContainer: {
    alignSelf: 'center',
    marginBottom: 15,
    backgroundColor: '#4a4a4a',
    borderRadius: 25,
    paddingVertical: 12,
    paddingHorizontal: 25,
  },
  title: {
    fontSize: 24,
    fontWeight: 'bold',
    color: 'white',
  },
  messageContainer: {
    alignItems: 'center',
    marginVertical: 10,
  },
  messageText: {
    fontSize: 24,
    color: '#4a4a4a',
    marginVertical: 2,
  },
  highlightText: {
    color: '#ffc100',
    fontWeight: 'bold',
    fontSize: 20,
  },
  imageContainer: {
    marginVertical: 20,
    alignItems: 'center',
    paddingBottom: 10,
  },
  hoseImage: {
    width: 300,
    height: 300,
  },
  instructionsContainer: {
    alignItems: 'center',
    marginBottom: 20,
  },
  instructionsText: {
    fontSize: 24,
    color: '#4a4a4a',
    lineHeight: 28,
    textAlign: 'center',
  },
  spacedText: {
    marginTop: 25,
  },
  startButton: {
    backgroundColor: '#ffc100',
    paddingHorizontal: 30,
    paddingVertical: 15,
    borderRadius: 20,
    marginTop: 20,
    marginBottom: 40,
    // iOS shadow properties
    shadowColor: '#000',
    shadowOffset: {
        width: 5,
        height: 8,
    },
    shadowOpacity: 0.25,
    shadowRadius: 3.84,
    // Android shadow
    elevation: 5,
},
  startButtonText: {
    color: '#4a4a4a',
    fontSize: 18,
    fontWeight: 'bold',
  },
  asteriskContainer: {
    position: 'relative',
    marginTop: 20,
    marginBottom: 30, // Adjust this value to move the text up or down
    left: 0,
    right: 0,
    alignItems: 'center',
    paddingHorizontal: 20,
  },
  asteriskText: {
    fontSize: 18,
    color: '#4a4a4a',
    textAlign: 'center',
  },
  copyrightText: {
    fontSize: 16,
    color: '#4a4a4a',
    textAlign: 'center',
    marginTop: 8,
    fontStyle: 'italic',
    opacity: 0.8,
    marginTop: 20,
  },
  modalOverlay: {
    flex: 1,
    justifyContent: 'center',
    alignItems: 'center',
    position: 'relative', 
  },
  overlayBackground: {
    position: 'absolute',
    top: 0,
    left: 0,
    right: 0,
    bottom: 0,
    backgroundColor: 'rgba(0, 0, 0, 0.7)',
  },
  selectionContainer: {
    position: 'relative', 
    width: '80%',
    backgroundColor: 'rgba(0, 0, 0, 0.7)',
    borderRadius: 25,
    padding: 20,
    alignItems: 'center',
    // iOS shadow
    shadowColor: '#000',
    shadowOffset: {
      width: 0,
      height: 2,
    },
    shadowOpacity: 0.25,
    shadowRadius: 3.84,
    // Android shadow
    elevation: 5,
  },
  selectionTitle: {
    fontSize: 24,
    fontWeight: 'bold',
    color: 'white',
    marginBottom: 20,
    textAlign: 'center',
  },
  selectionButton: {
    minWidth: 200, // Set a minimum width
    alignSelf: 'center', // Center the button
    backgroundColor: 'white', // Default white background
    paddingHorizontal: 30,
    paddingVertical: 15,
    borderRadius: 25,
    marginVertical: 10,
    borderWidth: 4,
    borderColor: '#4a4a4a',
    // iOS shadow
    shadowColor: '#000',
    shadowOffset: {
      width: 5,
      height: 8,
    },
    shadowOpacity: 0.25,
    shadowRadius: 3.84,
    // Android shadow
    elevation: 5,
  },
  selectedButton: {
    backgroundColor: '#ffc100',
  },
  selectionButtonText: {
    fontSize: 20,
    fontWeight: 'bold',
    color: '#4a4a4a',
    textAlign: 'center',
  },
  enquiryText: {
    fontSize: 16,
    color: '#4a4a4a',
    textAlign: 'center',
    marginTop: 8,
    marginBottom: 12,
  },
  contactText: {
    fontSize: 16,
    color: '#4a4a4a',
    textAlign: 'center',
    marginTop: 8,
    textDecorationLine: 'underline',
    paddingVertical: 10,
  },
});

export default WelcomeScreen;