// components/ScrollableGradientWrapper.js
import React, { useEffect, useRef } from 'react';
import { View, Platform } from 'react-native';

const ScrollableGradientWrapper = ({ children, style }) => {
  const wrapperRef = useRef(null);
  const gradientRef = useRef(null);

  useEffect(() => {
    if (Platform.OS === 'web') {
      const checkScroll = () => {
        if (!wrapperRef.current || !gradientRef.current) return;

        const wrapper = wrapperRef.current;
        const scrollableDistance = wrapper.scrollHeight - wrapper.clientHeight;
        const currentScroll = wrapper.scrollTop;
        const isScrollable = scrollableDistance > 0;
        const isAtBottom = Math.ceil(currentScroll) >= Math.floor(scrollableDistance);

        // Show/hide gradient
        if (gradientRef.current) {
          gradientRef.current.style.opacity = (isScrollable && !isAtBottom) ? '1' : '0';
        }
      };

      // Create ResizeObserver to monitor content size changes
      const resizeObserver = new ResizeObserver((entries) => {
        checkScroll();
      });

      // Initial checks with multiple timings
      const initialChecks = () => {
        checkScroll();
        setTimeout(checkScroll, 100);
        setTimeout(checkScroll, 500);
        setTimeout(checkScroll, 1000);
      };

      if (wrapperRef.current) {
        // Observe size changes
        resizeObserver.observe(wrapperRef.current);
        
        // Add scroll listener
        wrapperRef.current.addEventListener('scroll', checkScroll);
        
        // Watch for content changes
        const mutationObserver = new MutationObserver(initialChecks);
        mutationObserver.observe(wrapperRef.current, {
          childList: true,
          subtree: true
        });

        // Initial check
        initialChecks();
      }

      // Handle window resize
      window.addEventListener('resize', checkScroll);

      // Cleanup
      return () => {
        if (wrapperRef.current) {
          wrapperRef.current.removeEventListener('scroll', checkScroll);
        }
        resizeObserver.disconnect();
        window.removeEventListener('resize', checkScroll);
      };
    }
  }, []);

  if (Platform.OS === 'web') {
    return (
      <div
        ref={wrapperRef}
        style={{
          height: '100vh',
          overflowY: 'auto',
          position: 'relative',
          backgroundColor: 'white',
          ...style,
          isolation: 'isolate',
        }}
      >
        <div style={{ 
          minHeight: '100%',
          paddingBottom: '100px',
          position: 'relative',
          zIndex: 1
        }}>
          {children}
        </div>
        <div 
          ref={gradientRef}
          style={{
            position: 'fixed',
            bottom: 0,
            left: 0,
            right: 0,
            height: '120px',
            background: 'linear-gradient(to bottom, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 0.8) 30%, rgba(255, 255, 255, 1) 100%)',
            pointerEvents: 'none',
            zIndex: 2,
            transition: 'opacity 0.3s ease',
            opacity: '0'
          }} 
        />
      </div>
    );
  }

  return <View style={style}>{children}</View>;
};

export default ScrollableGradientWrapper;