// components/ScreenWrapper.js
import React from 'react';
import { Platform } from 'react-native';

const ScreenWrapper = ({ children }) => {
  // Only apply web-specific styles on web platform
  if (Platform.OS === 'web') {
    return (
      <div 
        style={{
          minHeight: '100vh',
          maxHeight: '100vh',
          overflowY: 'auto',
          WebkitOverflowScrolling: 'touch',
          backgroundColor: '#fff',
        }}
      >
        {children}
      </div>
    );
  }

  // Return children as-is for mobile platforms
  return children;
};

export default ScreenWrapper;