import React, { useState, useEffect, useRef } from 'react';
import { View, Text, StyleSheet, TouchableOpacity, ScrollView, SafeAreaView, Image, Dimensions, Platform, Animated } from 'react-native';
import { useNavigation, useRoute, useFocusEffect } from '@react-navigation/native';
import ScrollableGradientWrapper from '../components/ScrollableGradientWrapper';

const { width: SCREEN_WIDTH } = Dimensions.get('window');
const BUTTON_WIDTH = Math.min(320, SCREEN_WIDTH * 0.8);

// Price Display Component
const PriceDisplay = ({ price, hasTestingSelected, titleFade, priceFade }) => (
  <View style={styles.pricingCard}>
    <Animated.Text style={[styles.pricingHeader, { opacity: titleFade }]}>
      {hasTestingSelected ? 'Price with Pressure Testing' : 'Total Hose and Fitting Price'}
    </Animated.Text>
    <View style={styles.pricingValueContainer}>
      <Animated.Text style={[styles.pricingValue, { opacity: priceFade }]}>
        A$ {price.toFixed(2)}
      </Animated.Text>
    </View>
  </View>
);

const PressureButton = ({ type, onPress, isSelected }) => (
  <TouchableOpacity 
    style={[styles.pressureButton, isSelected && styles.selectedPressureButton]} 
    onPress={onPress}
  >
    <Text style={styles.pressureButtonText}>{type}</Text>
  </TouchableOpacity>
);

const PressureTestingScreen = () => {
  const navigation = useNavigation();
  const route = useRoute();
  const [selectedPressure, setSelectedPressure] = useState(null);
  const [initialPrice, setInitialPrice] = useState(0);
  const [pressureTestPrice, setPressureTestPrice] = useState(0);
  const priceFade = useRef(new Animated.Value(1)).current;
  const fadeAnim = useRef(new Animated.Value(0)).current;
  const titleFade = useRef(new Animated.Value(1)).current;

  const { 
    selectedHose,
    selectedHosePrice,
    selectedFitting, 
    end1Shape, 
    end1Size,
    end1Price, 
    selectedEnd2Fitting, 
    end2Shape, 
    end2Size,
    end2Price, 
    selectedAngle,
    quantity,
    cutLengths,
    selectedProtection,
    cutLengthPrice,
    basePrice,
    hoseProtectionPrice,
    protectionCost
  } = route.params || {};

  const pressureOptions = [
    'Not Required',
    '3000psi',
    '4000psi',
    '5000psi',
    '6000psi'
  ];

  // Set initial price
  useEffect(() => {
    if (hoseProtectionPrice) {
      setInitialPrice(hoseProtectionPrice);  // Show price from Hose Protection
      setPressureTestPrice(hoseProtectionPrice);
    }
  }, [hoseProtectionPrice]);

  useEffect(() => {
    Animated.timing(fadeAnim, {
      toValue: 1,
      duration: 800,
      useNativeDriver: Platform.OS === 'native',
    }).start();
  }, []);

  useFocusEffect(
    React.useCallback(() => {
      fadeAnim.setValue(0);
      const timer = setTimeout(() => {
        Animated.timing(fadeAnim, {
          toValue: 1,
          duration: 400,
          useNativeDriver: Platform.OS === 'native',
        }).start();
      }, 50);

      return () => clearTimeout(timer);
    }, [fadeAnim])
  );

  const handlePressureSelection = (pressure) => {
    setSelectedPressure(pressure);
    
    if (pressure === 'Not Required') {
      // Simple navigation with current price from Hose Protection
      Animated.timing(fadeAnim, {
        toValue: 0,
        duration: 800,
        useNativeDriver: Platform.OS === 'native',
      }).start(() => {
        navigateToNext(pressure, initialPrice);
      });
    } else {
      // Parallel animation for title and price
      Animated.sequence([
        Animated.parallel([
          // Fade out both title and price
          Animated.timing(titleFade, {
            toValue: 0,
            duration: 500,
            useNativeDriver: Platform.OS === 'native',
          }),
          Animated.timing(priceFade, {
            toValue: 0,
            duration: 500,
            useNativeDriver: Platform.OS === 'native',
          })
        ]),
        Animated.timing(new Animated.Value(0), {
          toValue: 1,
          duration: 100,
          useNativeDriver: Platform.OS === 'native',
        }),
      ]).start(() => {
        // Calculate pressure test cost (Fixes Price of A$ 10)
        const pressureTestCost = 10;
        // Add this to the price from Hose Protection
        const newPrice = hoseProtectionPrice + pressureTestCost;
        setPressureTestPrice(newPrice);
        
        // Fade in both title and price
        Animated.parallel([
          Animated.timing(titleFade, {
            toValue: 1,
            duration: 500,
            useNativeDriver: Platform.OS === 'native',
          }),
          Animated.timing(priceFade, {
            toValue: 1,
            duration: 500,
            useNativeDriver: Platform.OS === 'native',
          })
        ]).start(() => {
          // Start navigation animation after price update
          setTimeout(() => {
            Animated.timing(fadeAnim, {
              toValue: 0,
              duration: 800,
              useNativeDriver: Platform.OS === 'native',
            }).start(() => {
              navigateToNext(pressure, newPrice);
            });
          }, 1000);
        });
      });
    }
  };

  const navigateToNext = (pressure, price) => {
    navigation.navigate('ShippingOptions', {
      selectedPressure: pressure,
      selectedProtection,
      selectedHose,
      selectedHosePrice,
      selectedFitting,
      end1Shape,
      end1Size,
      end1Price,
      selectedEnd2Fitting,
      end2Shape,
      end2Size,
      end2Price,
      selectedAngle,
      quantity,
      cutLengths,
      cutLengthPrice,
      basePrice,
      hoseProtectionPrice: price,  // Pass the new total price as hoseProtectionPrice
      protectionCost,
      pressureTestPrice: pressure === 'Not Required' ? 0 : 10  // Fixes fee of A$10
    });
  };

  return (
    <View style={styles.mainContainer}>
      {/* Floating Back Button */}
      <TouchableOpacity 
        style={styles.floatingBackButton}
        onPress={() => navigation.goBack()}
      >
        <Text style={styles.backButtonText}></Text>
      </TouchableOpacity>
      <ScrollableGradientWrapper>
        <SafeAreaView style={styles.container}>
        <View style={styles.logoContainer}>
          <Image
            source={require('../assets/fluidpower_logo.gif')}
            style={styles.logo}
            resizeMode="contain"
          />
        </View>
        <Animated.View style={[styles.animatedContainer, { opacity: fadeAnim }]}>
          <View style={styles.titleContainer}>
            <View style={styles.titleWrapper}>
              <Text style={styles.titleText}>Pressure TESTING</Text>
              <Text style={styles.titleText}>(Optional)</Text>
            </View>
          </View>
          <Image
            source={require('../assets/pressure_testing_diagram.png')}
            style={styles.diagram}
            resizeMode="contain"
          />
          <View style={styles.subtitleContainer}>
            <Text style={styles.subtitleText}>
              Get your Hose Pressure Tested for an additional A$ 10.00 per Hose
            </Text>
          </View>
          <View style={styles.buttonsContainer}>
            {pressureOptions.map((pressure, index) => (
              <PressureButton
                key={index}
                type={pressure}
                onPress={() => handlePressureSelection(pressure)}
                isSelected={selectedPressure === pressure}
              />
            ))}
          </View>

          <View style={styles.pricingSection}>
            <PriceDisplay 
              price={selectedPressure && selectedPressure !== 'Not Required' ? pressureTestPrice : initialPrice}
              hasTestingSelected={selectedPressure && selectedPressure !== 'Not Required'}
              titleFade={titleFade}
              priceFade={priceFade}
            />
          </View>
        </Animated.View>
        </SafeAreaView>
      </ScrollableGradientWrapper>
    </View>
  );
};

