import React from 'react';
import { 
  View, 
  Text, 
  StyleSheet, 
  SafeAreaView, 
  ScrollView,
  TouchableOpacity,
  Share
} from 'react-native';
import { useNavigation } from '@react-navigation/native';

const OrderDetailsScreen = ({ route }) => {
  const { orderDetails } = route.params;
  const navigation = useNavigation();

  // Extract order information
  const {
    userDetails,
    paymentId,
    amount,
    selectedHose,
    selectedHosePrice,
    selectedFitting,
    end1Shape,
    end1Size,
    end1Price,
    selectedEnd2Fitting,
    end2Shape,
    end2Size,
    end2Price,
    selectedAngle,
    quantity,
    cutLengths,
    cutLengthPrice, isOrderFittingMode,
    selectedProtection,
    hoseProtectionPrice,
    selectedPressure,
    pressureTestPrice,
    selectedMethod,
    selectedService,
    shippingCost,
  } = orderDetails;

  // Function to share order details
  const handleShareOrder = async () => {
    try {
      const shareMessage = `
Order Details
Order ID: ${paymentId}
Amount: A$${amount}
Status: Confirmed

Product Details:
- Hose: ${selectedHose}
- Fittings: ${selectedFitting}
- End 1: ${end1Shape} ${end1Size}
- End 2: ${selectedEnd2Fitting} ${end2Shape} ${end2Size}
- Quantity: ${quantity}
`;

      await Share.share({
        message: shareMessage,
        title: `Order #${paymentId}`,
      });
    } catch (error) {
      console.error('Error sharing order:', error);
    }
  };

  const renderSection = (title, content) => (
    <View style={styles.section}>
      <Text style={styles.sectionTitle}>{title}</Text>
      <View style={styles.sectionContent}>
        {content}
      </View>
    </View>
  );

  return (
    <SafeAreaView style={styles.container}>
      <ScrollView contentContainerStyle={styles.scrollContent}>
        {/* Order ID and Status */}
        <View style={styles.headerSection}>
          <Text style={styles.orderId}>Order #{paymentId}</Text>
          <View style={styles.statusBadge}>
            <Text style={styles.statusText}>Confirmed</Text>
          </View>
        </View>

        {/* Product Details */}
        {renderSection('Product Details', (
          <>
            <View style={styles.detailRow}>
              <Text style={styles.detailLabel}>Hose Type:</Text>
              <Text style={styles.detailValue}>{selectedHose}</Text>
            </View>
            <View style={styles.detailRow}>
              <Text style={styles.detailLabel}>Fitting Type:</Text>
              <Text style={styles.detailValue}>{selectedFitting}</Text>
            </View>
            <View style={styles.detailRow}>
              <Text style={styles.detailLabel}>End 1:</Text>
              <Text style={styles.detailValue}>{`${end1Shape} ${end1Size}`}</Text>
            </View>
            <View style={styles.detailRow}>
              <Text style={styles.detailLabel}>End 2:</Text>
              <Text style={styles.detailValue}>{`${end2Shape} ${end2Size}`}</Text>
            </View>
            <View style={styles.detailRow}>
              <Text style={styles.detailLabel}>Angle:</Text>
              <Text style={styles.detailValue}>{selectedAngle}</Text>
            </View>
            <View style={styles.detailRow}>
              <Text style={styles.detailLabel}>Quantity:</Text>
              <Text style={styles.detailValue}>{quantity}</Text>
            </View>
          </>
        ))}

        {/* Additional Services */}
        {renderSection('Additional Services', (
          <>
            {selectedProtection && (
              <View style={styles.detailRow}>
                <Text style={styles.detailLabel}>Protection:</Text>
                <Text style={styles.detailValue}>{selectedProtection}</Text>
              </View>
            )}
            {selectedPressure && (
              <View style={styles.detailRow}>
                <Text style={styles.detailLabel}>Pressure Test:</Text>
                <Text style={styles.detailValue}>{selectedPressure}</Text>
              </View>
            )}
            {selectedMethod && (
              <View style={styles.detailRow}>
                <Text style={styles.detailLabel}>Delivery Method:</Text>
                <Text style={styles.detailValue}>{selectedMethod}</Text>
              </View>
            )}
          </>
        ))}

        {/* Price Breakdown */}
        {renderSection('Price Breakdown', (
          <>
            <View style={styles.priceRow}>
              <Text style={styles.priceLabel}>Hose:</Text>
              <Text style={styles.priceValue}>A${selectedHosePrice.toFixed(2)}</Text>
            </View>
            <View style={styles.priceRow}>
              <Text style={styles.priceLabel}>End 1:</Text>
              <Text style={styles.priceValue}>A${end1Price.toFixed(2)}</Text>
            </View>
            <View style={styles.priceRow}>
              <Text style={styles.priceLabel}>End 2:</Text>
              <Text style={styles.priceValue}>A${end2Price.toFixed(2)}</Text>
            </View>
            {hoseProtectionPrice > 0 && (
              <View style={styles.priceRow}>
                <Text style={styles.priceLabel}>Protection:</Text>
                <Text style={styles.priceValue}>A${hoseProtectionPrice.toFixed(2)}</Text>
              </View>
            )}
            {pressureTestPrice > 0 && (
              <View style={styles.priceRow}>
                <Text style={styles.priceLabel}>Pressure Test:</Text>
                <Text style={styles.priceValue}>A${pressureTestPrice.toFixed(2)}</Text>
              </View>
            )}
            <View style={styles.priceRow}>
              <Text style={styles.priceLabel}>Shipping:</Text>
              <Text style={styles.priceValue}>A${shippingCost.toFixed(2)}</Text>
            </View>
            <View style={[styles.priceRow, styles.totalRow]}>
              <Text style={styles.totalLabel}>Total:</Text>
              <Text style={styles.totalValue}>A${amount}</Text>
            </View>
          </>
        ))}

        {/* Shipping Details */}
        {renderSection('Shipping Details', (
          <>
            <View style={styles.detailRow}>
              <Text style={styles.detailLabel}>Name:</Text>
              <Text style={styles.detailValue}>{userDetails.name}</Text>
            </View>
            {userDetails.companyName && (
              <View style={styles.detailRow}>
                <Text style={styles.detailLabel}>Company:</Text>
                <Text style={styles.detailValue}>{userDetails.companyName}</Text>
              </View>
            )}
            <View style={styles.detailRow}>
              <Text style={styles.detailLabel}>Address:</Text>
              <Text style={styles.detailValue}>{userDetails.address}</Text>
            </View>
            <View style={styles.detailRow}>
              <Text style={styles.detailLabel}>Suburb:</Text>
              <Text style={styles.detailValue}>{userDetails.suburb}</Text>
            </View>
            <View style={styles.detailRow}>
              <Text style={styles.detailLabel}>State:</Text>
              <Text style={styles.detailValue}>{userDetails.state}</Text>
            </View>
            <View style={styles.detailRow}>
              <Text style={styles.detailLabel}>Postcode:</Text>
              <Text style={styles.detailValue}>{userDetails.postcode}</Text>
            </View>
          </>
        ))}

        {/* Action Buttons */}
        <View style={styles.actionButtons}>
          <TouchableOpacity 
            style={styles.shareButton}
            onPress={handleShareOrder}
          >
            <Text style={styles.shareButtonText}>Share Order Details</Text>
          </TouchableOpacity>
          
          <TouchableOpacity 
            style={styles.newOrderButton}
            onPress={() => navigation.navigate('CreateOrder')}
          >
            <Text style={styles.newOrderButtonText}>Place New Order</Text>
          </TouchableOpacity>
        </View>
      </ScrollView>
    </SafeAreaView>
  );
};

