// components/Prices/HosePrices.js

const BASE_URL = process.env.REACT_APP_API_URL; // Add fallback URL
const API_CLIENT_KEY = process.env.REACT_APP_API_CLIENT_KEY;

const DEFAULT_HOSE_PRICES = {
  "1/4": "16.50",
  "3/8": "18.50",
  "1/2": "22.50",
  "5/8": "28.50",
  "3/4": "36.50",
  "1": "48.50"
};

export const getHosePrices = async () => {
  try {

      // Use full URL instead of relative path
        const response = await fetch(`${BASE_URL}/api/get-prices`, {
          method: 'GET',
          headers: {
              'Accept': 'application/json',
              'Content-Type': 'application/json',
              'x-api-key': API_CLIENT_KEY
          }
      });
      
      if (!response.ok) {
          const errorText = await response.text();
          console.error('Error response:', errorText);
          throw new Error(`HTTP error! status: ${response.status}`);
      }
      
      const text = await response.text();
      
      const data = JSON.parse(text);
      return data.hosePrices || DEFAULT_HOSE_PRICES;
  } catch (error) {
      console.error('Error fetching Hose prices:', error);
      return DEFAULT_HOSE_PRICES;
  }
};