// components/PDFGenerator/WebPDFGenerator.js
import React from 'react';
import html2canvas from 'html2canvas';
import { jsPDF } from 'jspdf';

const WebPDFGenerator = ({ contentRef, onGenerateStart, onGenerateComplete, onError, orderId }) => {
  const generatePDF = async ({ silent = false } = {}) => {
    try {
      if (!contentRef.current) return;
      
      onGenerateStart?.();

      const element = contentRef.current;
      const canvas = await html2canvas(element, {
        scale: 2,
        useCORS: true,
        logging: false
      });

      const imgData = canvas.toDataURL('image/jpeg', 1.0);
      const pdf = new jsPDF({
        orientation: 'portrait',
        unit: 'px',
        format: [canvas.width, canvas.height]
      });

      pdf.addImage(imgData, 'JPEG', 0, 0, canvas.width, canvas.height);
      
      // Get PDF as buffer for email
      const pdfBuffer = pdf.output('arraybuffer');

      // If not silent, trigger download
      if (!silent) {
        // Generate filename using Order ID
        const filename = `Order_${orderId}.pdf`;
        
        // Create blob and URL for download
        const pdfBlob = new Blob([pdfBuffer], { type: 'application/pdf' });
        const pdfUrl = URL.createObjectURL(pdfBlob);
        
        // Create and trigger download link
        const link = document.createElement('a');
        link.href = pdfUrl;
        link.download = filename;
        document.body.appendChild(link);
        link.click();
        document.body.removeChild(link);
        
        // Clean up
        URL.revokeObjectURL(pdfUrl);
      }
      
      // Call onGenerateComplete with the PDF buffer
      onGenerateComplete?.(pdfBuffer);
      
      return pdfBuffer;
    } catch (error) {
      console.error('Error generating PDF:', error);
      onError?.(error);
      return null;
    }
  };

  return { generatePDF };
};

export default WebPDFGenerator;