// components/Prices/MetricPrices.js

const BASE_URL = process.env.REACT_APP_API_URL;
const API_CLIENT_KEY = process.env.REACT_APP_API_CLIENT_KEY;

const DEFAULT_METRIC_PRICES = {
    "M12": "6.50",
    "M14": "7.50",
    "M16": "9.50",
    "M18": "10.00",
    "M22": "12.50",
    "M26": "16.50",
    "M30": "26.50",
    "M36": "34.50"
};

export const getMetricPrices = async () => {
    try {

        const response = await fetch(`${BASE_URL}/api/get-prices`, {
            method: 'GET',
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json',
                'x-api-key': API_CLIENT_KEY
            }
        });
        
        if (!response.ok) {
            const errorText = await response.text();
            console.error('Error response:', errorText);
            throw new Error(`HTTP error! status: ${response.status}`);
        }
        
        const text = await response.text();
        
        const data = JSON.parse(text);
        return data.metricPrices || DEFAULT_METRIC_PRICES;
    } catch (error) {
        console.error('Error fetching Metric prices:', error);
        return DEFAULT_METRIC_PRICES;
    }
};