import React, { useState, useRef } from 'react';
import { View, Text, StyleSheet, SafeAreaView, Image, Dimensions, Platform, TextInput, ScrollView, Animated, TouchableOpacity } from 'react-native';
import { useNavigation, useRoute, useFocusEffect } from '@react-navigation/native';
import { Picker } from '@react-native-picker/picker';
import ScrollableGradientWrapper from '../components/ScrollableGradientWrapper';

const { width: SCREEN_WIDTH } = Dimensions.get('window');

const STATES = ['Select State', 'ACT', 'NSW', 'NT', 'QLD', 'SA', 'TAS', 'VIC', 'WA'];

const ShippingDetailsScreen = () => {
  const navigation = useNavigation();
  const route = useRoute();
  const buttonRef = React.useRef(null);
  const [isSelected, setIsSelected] = React.useState(false);
  const [hadTenDigits, setHadTenDigits] = useState(false);
  const [formSubmitAttempted, setFormSubmitAttempted] = useState(false);

  const { 
    selectedHose,
        selectedHosePrice,
        selectedFitting,
        end1Shape,
        end1Size,
        end1Price,
        selectedEnd2Fitting,
        end2Shape,
        end2Size,
        end2Price,
        selectedAngle,
        quantity,
        cutLengths,
        cutLengthPrice, isOrderFittingMode,
        selectedProtection,
        selectedPressure,
        selectedMethod,
        selectedService,
        basePrice,
        hoseProtectionPrice,
        pressureTestPrice,
        protectionCost,
        shippingCost,
        totalBeforeShipping,
        totalPriceWithShipping
  } = route.params || {};
  
  // State for form fields
  const [formFields, setFormFields] = useState({
    name: '',
    companyName: '',
    address: '',
    suburb: '',
    state: 'Select State', // Default state selection
    postcode: '',
    email: '',
    contactNumber: ''
  });

  const [touchedFields, setTouchedFields] = useState({
    name: false,
    companyName: false,
    address: false,
    suburb: false,
    state: false,
    postcode: false,
    email: false,
    contactNumber: false
  })
  
  const [errors, setErrors] = useState({
    name: '',
    companyName: '',
    address: '',
    suburb: '',
    state: '',
    postcode: '',
    email: '',
    contactNumber: ''
  });

  const [allFieldsFilled, setAllFieldsFilled] = useState(false);
  const inputRefs = useRef([]);
  const fadeAnim = useRef(new Animated.Value(0)).current;

  // Initial fade-in animation
  React.useEffect(() => {
    Animated.timing(fadeAnim, {
      toValue: 1,
      duration: 800,
      useNativeDriver: Platform.OS === 'native',
    }).start();
  }, []);

  // Screen focus animation and cleanup
  useFocusEffect(
    React.useCallback(() => {
      fadeAnim.setValue(0);
      setIsSelected(false);
      Animated.timing(fadeAnim, {
        toValue: 1,
        duration: 800,
        useNativeDriver: Platform.OS === 'native',
      }).start();

      return () => {
        // Cleanup if needed
      };
    }, [fadeAnim])
  );

  React.useEffect(() => {
    // Small delay to ensure the input is mounted
    const timer = setTimeout(() => {
      if (inputRefs.current[0]) {
        inputRefs.current[0].focus();
      }
    }, 100);
  
    return () => clearTimeout(timer);
  }, []);

  {/*
  // Check if all required fields are filled
  React.useEffect(() => {
    const requiredFields = {
      name: formFields.name,
      address: formFields.address,
      suburb: formFields.suburb,
      state: formFields.state !== 'Select State' ? formFields.state : '',
      postcode: formFields.postcode,
      email: formFields.email,
      //contactNumber: formFields.contactNumber 
    };

    const isFilled = Object.entries(requiredFields).every(([key, value]) => value.trim() !== '');

    const isContactNumberValid = 
    formFields.contactNumber.length === 10 && 
    /^\d{10}$/.test(formFields.contactNumber);
  
    const hasNoErrors = Object.values(errors).every(error => error === '');
    
    setAllFieldsFilled(isFilled && hasNoErrors && isContactNumberValid);
  }, [formFields, errors]);
*/}

  const validateField = (name, value) => {
    let error = '';
    
    switch (name) {
      case 'name':
      case 'address':
        if (value.trim() === '') {
          error = 'This field is required';
        }
        break;

      case 'suburb':
        if (value.trim() === '') {
          error = 'This field is required';
        } else if (!/^[a-zA-Z\s]+$/.test(value)) {
          error = 'Please enter letters only';
        }
        break;

      case 'state':
        if (value === 'Select State') {
          error = 'Please select a state';
        }
        break;

      case 'postcode':
        if (value.trim() === '') {
          error = 'This field is required';
        } else if (!/^\d{4}$/.test(value)) {
          error = 'Please enter 4 digits';
        }
        break;

      case 'email':
        if (value.trim() === '') {
          error = 'This field is required';
        } else {
          const emailRegex = /^[a-zA-Z0-9._]+@[a-zA-Z]{2,}\.[a-zA-Z]{2,}(\.[a-zA-Z]{2,})*$/;
          if (!emailRegex.test(value)) {
            error = 'Please enter a valid email address';
          }
        }
        break;

      case 'contactNumber':
        if (value.trim() === '') {
          error = 'This field is required';
        } else if (!/^\d{10}$/.test(value)) {
          error = 'Contact number must be 10 digits';
        }
        break;
    }
    
    return error;
  };

  const updateField = (name, value) => {
    setFormFields(prev => ({
      ...prev,
      [name]: value
    }));

    // Only show errors if form submission was attempted
    if (formSubmitAttempted) {
      const error = validateField(name, value);
      setErrors(prev => ({
        ...prev,
        [name]: error
      }));
    }
  };

  const handleNext = () => {
    setFormSubmitAttempted(true);
    
    // Validate all required fields
    const newErrors = {};
    let hasErrors = false;
    
    Object.entries(formFields).forEach(([fieldName, value]) => {
      if (fieldName !== 'companyName') { // Skip optional fields
        const error = validateField(fieldName, value);
        newErrors[fieldName] = error;
        if (error) hasErrors = true;
      }
    });
    
    setErrors(newErrors);

    if (!hasErrors) {
      setIsSelected(true);
      // Start the fade out animation
      Animated.timing(fadeAnim, {
        toValue: 0,
        duration: 800,
        useNativeDriver: Platform.OS === 'native',
      }).start(() => {
        // Navigate after animation completes
        navigation.navigate('Payment', {
        // Previous parameters from route
        selectedHose,
        selectedHosePrice,
        selectedFitting,
        end1Shape,
        end1Size,
        end1Price,
        selectedEnd2Fitting,
        end2Shape,
        end2Size,
        end2Price,
        selectedAngle,
        quantity,
        cutLengths,
        cutLengthPrice, isOrderFittingMode,
        selectedProtection,
        hoseProtectionPrice,
        selectedPressure,
        pressureTestPrice,
        selectedMethod,
        selectedService,
        basePrice,
        shippingCost,
        totalBeforeShipping,
        totalPriceWithShipping,
        protectionCost,
        shippingDetails: formFields,
      });
    });
  }
};
      

const handleInputSubmit = (index) => {
  const currentFieldName = Object.keys(formFields)[index];
  
  // Show required field error immediately when skipping
  setTouchedFields(prev => ({
    ...prev,
    [currentFieldName]: true
  }));

  const currentValue = formFields[currentFieldName];
  const error = validateField(currentFieldName, currentValue, true);
  setErrors(prev => ({
    ...prev,
    [currentFieldName]: error
  }));

  if (index < 6) {
    inputRefs.current[index + 1].focus();
  }
};
    
const handleInputBlur = (name) => {
  setTouchedFields(prev => ({
    ...prev,
    [name]: true
  }));
  
  // Only show errors if form submission was attempted
  if (formSubmitAttempted) {
    const error = validateField(name, formFields[name]);
    setErrors(prev => ({
      ...prev,
      [name]: error
    }));
  }
};

    const renderInputField = (label, name, index, keyboardType = 'default', isOptional = false, additionalProps = {}) => (
      <View key={name} style={styles.inputRow}>
        <View style={styles.labelContainer}>
          <Text style={styles.labelText}>
            {label}{!isOptional && <Text style={styles.asterisk}>*</Text>}{isOptional ? ' (Optional)' : ''}
          </Text>
        </View>
        <View style={styles.inputContainer}>
          <TextInput
            ref={el => inputRefs.current[index] = el}
            style={[
              styles.input,
              formSubmitAttempted && errors[name] ? styles.inputError : null
            ]}
            value={formFields[name]}
            onChangeText={(text) => updateField(name, text)}
            onSubmitEditing={() => handleInputSubmit(index)}
            onBlur={() => handleInputBlur(name)}
            keyboardType={keyboardType}
            returnKeyType={index === 6 ? "done" : "next"}
            blurOnSubmit={index === 6}
            maxLength={name === 'postcode' ? 4 : additionalProps?.maxLength}
            placeholder={additionalProps?.placeholder}
            {...additionalProps}
          />
        </View>
        {formSubmitAttempted && errors[name] ? 
          <Text style={styles.errorText}>{errors[name]}</Text> : null}
      </View>
    );

    return (
      <View style={styles.mainContainer}>
        {/* Floating Back Button */}
        <TouchableOpacity 
          style={styles.floatingBackButton}
          onPress={() => navigation.goBack()}
        >
          <Text style={styles.backButtonText}></Text>
        </TouchableOpacity>
    
        <ScrollableGradientWrapper>
          <SafeAreaView style={styles.container}>
            <View style={styles.logoContainer}>
              <Image
                source={require('../assets/fluidpower_logo.gif')}
                style={styles.logo}
                resizeMode="contain"
              />
            </View>
            
            <Animated.View style={[styles.contentContainer, { opacity: fadeAnim }]}>
              <ScrollView 
                style={styles.mainScrollView}
                contentContainerStyle={styles.mainScrollViewContent}
                showsVerticalScrollIndicator={true}
              >
                <View style={styles.titleContainer}>
                  <Text style={styles.titleText}>Shipping Details</Text>
                </View>
                <Text style={styles.requiredFieldsText}>Fields marked with * are required</Text>
            
            <View style={styles.formContainer}>
              {renderInputField('Name', 'name', 0)}
              {renderInputField('Company Name', 'companyName', 1, 'default', true)}
              {renderInputField('Address', 'address', 2)}
              {renderInputField('Suburb', 'suburb', 3)}
      
              {/* State Picker */}
              <View style={styles.inputRow}>
                <View style={styles.labelContainer}>
                  <Text style={styles.labelText}>
                    State<Text style={styles.asterisk}>*</Text>
                  </Text>
                </View>
                <View style={styles.pickerContainer}>
                  <Picker
                    selectedValue={formFields.state}
                    onValueChange={(itemValue) => updateField('state', itemValue)}
                    style={styles.picker}
                    itemStyle={{ fontSize: 20, fontWeight: 'bold' }}
                  >
                    {STATES.map((state) => (
                      <Picker.Item 
                        key={state} 
                        label={state} 
                        value={state}
                        style={{ fontSize: 20, fontWeight: 'bold' }}
                      />
                    ))}
                  </Picker>
                </View>
                {formSubmitAttempted && errors.state ? 
                  <Text style={styles.errorText}>{errors.state}</Text> : null}
              </View>
              
              {renderInputField('Postcode', 'postcode', 4, 'numeric')}
              {renderInputField('Email', 'email', 5, 'email-address')}
              {renderInputField('Contact Number', 'contactNumber', 6, 'numeric', false, { maxLength: 10 })}
              
              {/* Proceed to Payment Button - Always visible */}
              <TouchableOpacity 
                style={[
                  styles.proceedButton,
                  { backgroundColor: isSelected ? '#ffc100' : 'transparent' },
                  { borderColor: '#4a4a4a' }
                ]}
                onPress={handleNext}
                activeOpacity={0.8}
              >
                <Text style={styles.proceedButtonText}>Proceed to Payment</Text>
              </TouchableOpacity>
              </View>
          </ScrollView>
        </Animated.View>
      </SafeAreaView>
    </ScrollableGradientWrapper>
  </View>
)
};

