import React, { useState, useEffect, useRef } from 'react';
import { View, Text, StyleSheet, TouchableOpacity, ScrollView, SafeAreaView, Image, Dimensions, Platform, Animated, } from 'react-native';
import { useNavigation, useRoute, useFocusEffect } from '@react-navigation/native';
import ScrollableGradientWrapper from '../components/ScrollableGradientWrapper';

const { width: SCREEN_WIDTH } = Dimensions.get('window');
const BUTTON_WIDTH = Math.min(320, SCREEN_WIDTH * 0.8); // Button width, capped at 320

const ProtectionTypeButton = ({ type, image, onPress, isSelected, priceText }) => (
  <TouchableOpacity 
    style={[
      styles.protectionButton, 
      isSelected && styles.selectedProtectionButton
    ]} 
    onPress={onPress}
  >
    <View style={styles.protectionContent}>
      {image ? (
        <>
          <Text style={styles.protectionButtonText}>{type}</Text>
          <View style={styles.contentRow}>
            <View style={styles.imageContainer}>
              <Image source={image} style={styles.protectionImage} resizeMode="contain" />
            </View>
            <View style={styles.priceContainer}>
              <View style={[styles.priceBorder, isSelected && styles.selectedPriceBorder]}>
                <Text style={[styles.priceText, isSelected && styles.selectedPriceText]}>
                  {priceText}
                </Text>
              </View>
            </View>
          </View>
        </>
      ) : (
        <View style={styles.noImageContentCentered}>
          <Text style={styles.protectionButtonText}>{type}</Text>
        </View>
      )}
    </View>
  </TouchableOpacity>
);

const PriceDisplay = ({ price, isHoseOnly, hasProtection, titleFade, priceFade }) => (
    <View style={styles.pricingCard}>
      <Animated.Text style={[styles.pricingHeader, { opacity: titleFade }]}>
        {hasProtection ? 'Hose Price with Protection' : (isHoseOnly ? 'Hose Only Price' : 'Total Hose and Fitting Price')}
      </Animated.Text>
      <View style={styles.pricingValueContainer}>
        <Animated.Text style={[styles.pricingValue, { opacity: priceFade }]}>
          A$ {price.toFixed(2)}
        </Animated.Text>
      </View>
    </View>
  );

  const HoseProtectionScreen = () => {
    const navigation = useNavigation();
    const route = useRoute();
    const [selectedProtection, setSelectedProtection] = useState(null);
    const [initialPrice, setInitialPrice] = useState(0);
    const [protectionPrice, setProtectionPrice] = useState(0);
    const priceFade = useRef(new Animated.Value(1)).current;
    const fadeAnim = useRef(new Animated.Value(0)).current;
    const titleFade = useRef(new Animated.Value(1)).current;
    
    const { 
      selectedHose, 
      selectedHosePrice,
      selectedFitting, 
      end1Shape, 
      end1Size,
      end1Price, 
      selectedEnd2Fitting, 
      end2Shape, 
      end2Size,
      end2Price, 
      selectedAngle, 
      quantity, 
      cutLengths,
      cutLengthPrice, 
      isHoseOnly
    } = route.params || {};
  
    // Move protectionTypes outside of useEffect
    const protectionTypes = [
      { type: 'NOT REQUIRED', image: null },
      { type: 'Nylon Hose Sleeve', image: require('../assets/nylon_sleeve.png') },
      { type: 'Plastic Spiral Cover', image: require('../assets/plastic_spiral.png') },
      { type: 'Metal Spiral Wrap', image: require('../assets/metal_spiral.png') },
    ];
  
    // First useEffect for logging
    useEffect(() => {
      console.log('Received in Protection Screen:', {
        hoseSize: selectedHose?.size,
        hosePrice: selectedHosePrice,
        quantity,
        cutLengths,
        isHoseOnly,
        cutLengthPrice
      });
    }, [selectedHose, selectedHosePrice, quantity, cutLengths, isHoseOnly]);

    useEffect(() => {
      if (cutLengthPrice) {
        setInitialPrice(cutLengthPrice);
        setProtectionPrice(cutLengthPrice);
      }
    }, [cutLengthPrice]);
  
    // Third useEffect for fade animation
    useEffect(() => {
      Animated.timing(fadeAnim, {
        toValue: 1,
        duration: 800,
        useNativeDriver: Platform.OS === 'native',
      }).start();
    }, []);
  
    useFocusEffect(
      React.useCallback(() => {
       // setSelectedProtection(null);
        fadeAnim.setValue(0);
        Animated.timing(fadeAnim, {
          toValue: 1,
          duration: 800,
          useNativeDriver: Platform.OS === 'native',
        }).start();
  
        return () => {
          // Optional cleanup if needed
        };
      }, [fadeAnim])
    );

    const PROTECTION_PRICE_MULTIPLIERS = {
      'Nylon Hose Sleeve': 1.30,  // 30% increase
      'Plastic Spiral Cover': 1.40, // 40% increase
      'Metal Spiral Wrap': 1.50,   // 50% increase
      'NOT REQUIRED': 1.0  // No increase
    };
  
    const handleProtectionSelection = (type) => {
      setSelectedProtection(type);
      
      if (type === 'NOT REQUIRED') {
        // Simple navigation with current price
        Animated.timing(fadeAnim, {
          toValue: 0,
          duration: 800,
          useNativeDriver: Platform.OS === 'native',
        }).start(() => {
          navigateToNext(type, initialPrice);
        });
      } else {
        // Parallel animation for title and price
        Animated.sequence([
          Animated.parallel([
            // Fade out both title and price
            Animated.timing(titleFade, {
              toValue: 0,
              duration: 500,
              useNativeDriver: Platform.OS === 'native',
            }),
            Animated.timing(priceFade, {
              toValue: 0,
              duration: 500,
              useNativeDriver: Platform.OS === 'native',
            })
          ]),
          Animated.timing(new Animated.Value(0), {
            toValue: 1,
            duration: 100,
            useNativeDriver: Platform.OS === 'native',
          }),
        ]).start(() => {
          // Update price state after fade out
          const multiplier = PROTECTION_PRICE_MULTIPLIERS[type];
          const newPrice = initialPrice * multiplier;
          setProtectionPrice(newPrice);
          
          // Fade in both title and price
          Animated.parallel([
            Animated.timing(titleFade, {
              toValue: 1,
              duration: 500,
              useNativeDriver: Platform.OS === 'native',
            }),
            Animated.timing(priceFade, {
              toValue: 1,
              duration: 500,
              useNativeDriver: Platform.OS === 'native',
            })
          ]).start(() => {
            // Start navigation animation after price update
            setTimeout(() => {
              Animated.timing(fadeAnim, {
                toValue: 0,
                duration: 800,
                useNativeDriver: Platform.OS === 'native',
              }).start(() => {
                navigateToNext(type, newPrice);
              });
            }, 1000);
          });
        });
      }
    };
    
    // Update the navigateToNext function to include protection cost calculation
    const navigateToNext = (type, price) => {
      const nextScreen = isHoseOnly ? 'ShippingOptions' : 'PressureTesting';
      navigation.navigate(nextScreen, {
        selectedProtection: type,
        selectedHose,
        selectedHosePrice,
        selectedFitting,
        end1Shape,
        end1Size,
        end1Price,
        selectedEnd2Fitting,
        end2Shape,
        end2Size,
        end2Price, 
        selectedAngle,
        cutLengths,
        quantity,
        isHoseOnly,
        cutLengthPrice: initialPrice, // Pass the original cut length price
        basePrice: initialPrice,
        hoseProtectionPrice: price,
        protectionCost: type === 'NOT REQUIRED' ? 0 : (price - initialPrice)
      });
    };
  
    return (
      <View style={styles.mainContainer}>
        {/* Floating Back Button */}
        <TouchableOpacity 
          style={styles.floatingBackButton}
          onPress={() => navigation.goBack()}
        >
          <Text style={styles.backButtonText}></Text>
        </TouchableOpacity>
        
        <ScrollableGradientWrapper>
          <SafeAreaView style={styles.container}>
            <View style={styles.logoContainer}>
              <Image
                source={require('../assets/fluidpower_logo.gif')}
                style={styles.logo}
                resizeMode="contain"
              />
            </View>
            
            <Animated.View style={[styles.animatedContainer, { opacity: fadeAnim }]}>
              <View style={styles.titleContainer}>
                <View style={styles.titleWrapper}>
                  <Text style={styles.titleText}>Hose PROTECTION</Text>
                  <Text style={styles.titleText}>(Optional)</Text>
                </View>
              </View>
    
              <View style={styles.subtitleContainer}>
                <Text style={styles.subtitleText}>Get your Hose Protected</Text>
                <Text style={styles.subtitleText}>Safety and Long Life</Text>
              </View>

              <View style={styles.subtitleContainer2}>
                <Text style={styles.subtitleText2}>*Hose Protection Prices Applied</Text>
                <Text style={styles.subtitleText2}>On Hose Lengths Only</Text>
              </View>
    
              <View style={styles.scrollViewContent}>
              {protectionTypes.map((protection, index) => (
                <ProtectionTypeButton
                  key={index}
                  type={protection.type}
                  image={protection.image}
                  onPress={() => handleProtectionSelection(protection.type)}
                  isSelected={selectedProtection === protection.type}
                  priceText={
                    protection.type === 'NOT REQUIRED' ? '' :
                    `A$ ${(initialPrice * (PROTECTION_PRICE_MULTIPLIERS[protection.type] - 1)).toFixed(2)}`
                  }
                />
              ))}
    
                <View style={styles.pricingSection}>
                  <PriceDisplay 
                    price={selectedProtection && selectedProtection !== 'NOT REQUIRED' ? protectionPrice : initialPrice}
                    isHoseOnly={isHoseOnly}
                    hasProtection={selectedProtection && selectedProtection !== 'NOT REQUIRED'}
                    titleFade={titleFade}
                    priceFade={priceFade}
                  />
                </View>
              </View>
            </Animated.View>
          </SafeAreaView>
        </ScrollableGradientWrapper>
      </View>
    );
   };

