const getEnvironmentConfig = async () => {
  try {
    if (typeof window !== 'undefined') {
      const API_BASE_URL = process.env.REACT_APP_API_URL;
      const response = await fetch(`${API_BASE_URL}/api/get-paypal-config`, {
        headers: {
          'x-server-key': process.env.REACT_APP_SERVER_KEY,
        },
      });

      if (!response.ok) {
        throw new Error('Failed to fetch PayPal config: ${response.status}');
      }

      return await response.json();
    }

    return {
      environment: process.env.EXPO_PUBLIC_PAYPAL_ENVIRONMENT || 'sandbox',
      sandboxEmail: process.env.EXPO_PUBLIC_PAYPAL_SANDBOX_EMAIL,
      productionEmail: process.env.EXPO_PUBLIC_PAYPAL_PRODUCTION_EMAIL
    };
  } catch (error) {
    console.error('Error fetching PayPal config:', error);
    return {
      environment: 'sandbox',
      sandboxEmail: process.env.EXPO_PUBLIC_PAYPAL_SANDBOX_EMAIL,
      productionEmail: process.env.EXPO_PUBLIC_PAYPAL_PRODUCTION_EMAIL
    };
  }
};

const PAYPAL_CONFIG = {
  IS_SANDBOX: true,
  MERCHANT_EMAIL: {
    SANDBOX: '',
    PRODUCTION: ''
  },
  BASE_URLS: {
    SANDBOX: 'https://www.sandbox.paypal.com/cgi-bin/webscr',
    PRODUCTION: 'https://www.paypal.com/cgi-bin/webscr'
  }
};

export const getPayPalConfig = async () => {
  try {
    const envConfig = await getEnvironmentConfig();

    PAYPAL_CONFIG.IS_SANDBOX = envConfig.environment === 'sandbox';
    PAYPAL_CONFIG.MERCHANT_EMAIL.SANDBOX = envConfig.sandboxEmail;
    PAYPAL_CONFIG.MERCHANT_EMAIL.PRODUCTION = envConfig.productionEmail;

    const mode = PAYPAL_CONFIG.IS_SANDBOX ? 'SANDBOX' : 'PRODUCTION';

    return {
      isSandbox: PAYPAL_CONFIG.IS_SANDBOX,
      merchantEmail: PAYPAL_CONFIG.MERCHANT_EMAIL[mode],
      baseUrl: PAYPAL_CONFIG.BASE_URLS[mode]
    };
  } catch (error) {
    console.error('Error in getPayPalConfig:', error);
    throw error;
  }
};