// components/Prices/ORFSPrices.js

const BASE_URL = process.env.REACT_APP_API_URL;
const API_CLIENT_KEY = process.env.REACT_APP_API_CLIENT_KEY;

const DEFAULT_ORFS_PRICES = {
    "9/16": "8.50",
    "11/16": "12.50",
    "13/16": "14.50",
    "1": "22.50",
    "1-3/16": "32.50"
};

export const getORFSPrices = async () => {
    try {

        const response = await fetch(`${BASE_URL}/api/get-prices`, {
            method: 'GET',
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json',
                'x-api-key': API_CLIENT_KEY
            }
        });
        
        if (!response.ok) {
            const errorText = await response.text();
            console.error('Error response:', errorText);
            throw new Error(`HTTP error! status: ${response.status}`);
        }
        
        const text = await response.text();
        
        const data = JSON.parse(text);
        return data.orfsPrices || DEFAULT_ORFS_PRICES;
    } catch (error) {
        console.error('Error fetching ORFS prices:', error);
        return DEFAULT_ORFS_PRICES;
    }
};