import React, { useState, useRef, useCallback, useMemo, useEffect } from 'react';
import { View, Text, StyleSheet, TouchableOpacity, ScrollView, SafeAreaView, Image, Dimensions, Animated, Platform } from 'react-native';
import { useNavigation, useRoute, useFocusEffect } from '@react-navigation/native';
import ScrollableGradientWrapper from '../components/ScrollableGradientWrapper';
import { getJICPrices } from '../components/Prices/JICPrices';

const { width: SCREEN_WIDTH } = Dimensions.get('window');
const MAX_COLUMN_WIDTH = 400; // Maximum width for each column
const BUTTON_WIDTH = Math.min(320, SCREEN_WIDTH * 0.8); // Button width, capped at 320

const ShapeButton = ({ subtitle, imagePath, onPress, isSelected }) => (
  <TouchableOpacity 
    style={[styles.shapeButton, isSelected && styles.selectedShapeButton]} 
    onPress={onPress}
  >
    <View style={styles.shapeButtonContent}>
    <Text style={styles.shapeButtonSubtitle}>{subtitle}</Text>
    </View>
    {imagePath && (
      <View style={styles.shapeButtonImageContainer}>
        <Image source={imagePath} style={styles.shapeButtonImage} resizeMode="contain" />
      </View>
    )}
  </TouchableOpacity>
);

const SizeButton = ({ title, price, onPress, isSelected }) => (
  <TouchableOpacity 
    style={[styles.sizeButton, isSelected && styles.selectedSizeButton]} 
    onPress={onPress}
  >
    <View style={styles.sizeButtonContent}>
      <View style={styles.sizeTextContainer}>
        <Text style={styles.sizeButtonText}>{title}</Text>
      </View>
      <View style={[styles.priceBorder, isSelected && styles.selectedPriceBorder]}>
        <Text style={[styles.priceText, isSelected && styles.selectedPriceText]}>A$ {price} ea</Text>
      </View>
    </View>
  </TouchableOpacity>
);

const JICFittingScreen2 = () => {
  const navigation = useNavigation();
  const route = useRoute();
  const [selectedShape, setSelectedShape] = useState(null);
  const [selectedSize, setSelectedSize] = useState(null);
  const { selectedHose, selectedHosePrice, selectedFitting, end1Shape, end1Size, end1Price, selectedEnd2Fitting } = route.params || {};
  const loadingFadeAnim = useRef(new Animated.Value(1)).current;
  const contentFadeAnim = useRef(new Animated.Value(0)).current;
  const [showContent, setShowContent] = useState(false);
  const [prices, setPrices] = useState(null);
  const [isLoading, setIsLoading] = useState(true);

  const shapes = [
    { name: 'JIC Male Straight', image: require('../assets/jic_male_straight.png') },
    { name: 'JIC Female Straight', image: require('../assets/jic_female_straight.png') },
    { name: 'JIC Female 45', image: require('../assets/jic_female_45.png') },
    { name: 'JIC Female 90', image: require('../assets/jic_female_90.png') },
  ];

  // Define all available sizes with prices
  const allSizes = useMemo(() => prices ? [
    { size: '7/16" - 20', basePrice: `${prices["7/16"]}` },
    { size: '9/16" - 18', basePrice: `${prices["9/16"]}` },
    { size: '3/4" - 16', basePrice: `${prices["3/4"]}` },
    { size: '7/8" - 14', basePrice: `${prices["7/8"]}` },
    { size: '1-1/16" - 12', basePrice: `${prices["1-1/16"]}` },
    { size: '1-3/16" - 12', basePrice: `${prices["1-3/16"]}` }
] : [], [prices]);

  // Define the JIC compatibility matrix from the image
  const compatibilityMatrix = {
    '1/4"': ['7/16"', '9/16"', '3/4"'],
    '3/8"': ['9/16"', '3/4"', '7/8"'],
    '1/2"': ['3/4"', '7/8"', '1-1/16"'],
    '5/8"': ['7/8"', '1-1/16"'],
    '3/4"': ['7/8"', '1-1/16"', '1-3/16"'],
    '1"': ['1-1/16"', '1-3/16"']
  };

  // Function to filter sizes based on selected hose size
  const getFilteredSizes = () => {
    const hoseSize = selectedHose.size;
    const compatibleSizes = compatibilityMatrix[hoseSize] || [];
    
    return allSizes.filter(sizeObj => 
      compatibleSizes.includes(sizeObj.size.split(' ')[0])
    );
  };

  const sizes = getFilteredSizes();

  useFocusEffect(
    useCallback(() => {
      // Start fade in animation when screen comes into focus
      Animated.timing(contentFadeAnim, {
        toValue: 1,
        duration: 500,
        useNativeDriver: Platform.OS === 'native',
      }).start();
  
      return () => {
        // Reset animation value when screen loses focus
        contentFadeAnim.setValue(0);
      };
    }, [contentFadeAnim])
  );

  useEffect(() => {
    const loadPrices = async () => {
      setIsLoading(true);
      setShowContent(false);
      loadingFadeAnim.setValue(1);
      contentFadeAnim.setValue(0);
      
      try {
        const fetchedPrices = await getJICPrices();
        setPrices(fetchedPrices);
        
        // Use sequence instead of parallel
        Animated.sequence([
          // First fade out loading
          Animated.timing(loadingFadeAnim, {
            toValue: 0,
            duration: 200,
            useNativeDriver: Platform.OS === 'native',
          }),
          // Small delay before content appears
          Animated.delay(100),
          // Then fade in content
          Animated.timing(contentFadeAnim, {
            toValue: 1,
            duration: 200,
            useNativeDriver: Platform.OS === 'native',
          })
        ]).start(() => {
          setIsLoading(false);
          setShowContent(true);
        });
      } catch (error) {
        console.error('Error loading prices:', error);
        setIsLoading(false);
      }
    };
    
    loadPrices();
  }, []);

  const handleShapeSelection = (shape) => {
    setSelectedShape(shape);
    setSelectedSize(null); // Reset size when a new shape is selected
  };

  const handleSizeSelection = (size) => {
    setSelectedSize(size);
    if (selectedShape && size) {
        const selectedSizeObj = sizes.find(sizeObj => sizeObj.size === size);
        // Strip the price to numeric form by removing spaces
        const priceNumeric = parseFloat(selectedSizeObj.basePrice.trim());
        handleNavigation(selectedShape, size, priceNumeric);
    }
};
  
  const handleNavigation = (shape, size, price) => {
    console.log('Navigation triggered with:', { shape, size, price, end1Shape, end1Size });
    
    Animated.timing(contentFadeAnim, {
      toValue: 0,
      duration: 500,
      useNativeDriver: Platform.OS === 'native',
    }).start(() => {
      const isStraightFitting = (shape) => shape.includes('Male Straight') || shape.includes('Female Straight');
      
      console.log('isStraightFitting(end1Shape):', isStraightFitting(end1Shape));
      console.log('isStraightFitting(shape):', isStraightFitting(shape));
  
      if (isStraightFitting(end1Shape) || isStraightFitting(shape)) {
        console.log('Navigating to CutLengths');
        navigation.navigate('CutLengths', { 
          selectedHose,
          selectedHosePrice, 
          selectedFitting,
          end1Shape, 
          end1Size,
          end1Price,
          selectedEnd2Fitting,
          end2Shape: shape,
          end2Size: size,
          end2Price: price, // Add the price to navigation params
        });
      } else {
        console.log('Navigating to OrientationSelectionScreen');
        navigation.navigate('OrientationSelectionScreen', { 
          selectedHose,
          selectedHosePrice,
          selectedFitting,
          end1Shape, 
          end1Size,
          end1Price,
          selectedEnd2Fitting,
          end2Shape: shape,
          end2Size: size,
          end2Price: price, // Add the price to navigation params
        });
      }
    });
  };

if (isLoading || !showContent) {
    return (
      <Animated.View style={[
        styles.mainContainer,
        styles.loadingContainer,
        { opacity: loadingFadeAnim }
      ]}>
        <View style={styles.loadingContent}>
          <Image
            source={require('../assets/fluidpower_logo.gif')}
            style={styles.loadingLogo}
            resizeMode="contain"
          />
          <Text style={styles.loadingText}>Fetching lowest prices for JIC Fittings ...</Text>
        </View>
      </Animated.View>
    );
}

  return (
    <Animated.View style={[styles.mainContainer, { opacity: contentFadeAnim } ]}>
      {/* Floating Back Button */}
      <TouchableOpacity 
        style={styles.floatingBackButton}
        onPress={() => navigation.goBack()}
      >
        <Text style={styles.backButtonText}></Text>
      </TouchableOpacity>
      
      <ScrollableGradientWrapper>
    <SafeAreaView style={styles.container}>
      <View style={styles.logoContainer}>
        <Image
          source={require('../assets/fluidpower_logo.gif')}
          style={styles.logo}
          resizeMode="contain"
        />
      </View>
      <Animated.View style={[styles.animatedContainer, { opacity: contentFadeAnim }]}>
        <View style={styles.titleContainer}>
          <View style={styles.titleWrapper}>
            <Text style={styles.titleText}>Select END </Text>
            <View style={styles.circleWrapper}>
              <Text style={styles.circleText}>2</Text>
            </View>
            <Text style={styles.titleText}> Thread</Text>
          </View>
        </View>
        <View style={styles.contentContainer}>
          <View style={styles.column}>
            <View style={styles.sectionTitleContainer}>
              <Text style={styles.sectionTitleText}>Select SHAPE</Text>
            </View>
            <ScrollView 
              contentContainerStyle={styles.scrollViewContent}
              style={styles.scrollView}
              showsVerticalScrollIndicator={false}
            >
              {shapes.map((shape, index) => (
                <ShapeButton
                  key={index}
                  title={shape.name.split(' ')[0]}
                  subtitle={shape.name}
                  imagePath={shape.image}
                  onPress={() => handleShapeSelection(shape.name)}
                  isSelected={selectedShape === shape.name}
                />
              ))}
            </ScrollView>
          </View>
          <View style={styles.column}>
            {selectedShape ? (
              <>
                <View style={styles.sectionTitleContainer}>
                  <Text style={styles.sectionTitleText}>Select SIZE</Text>
                </View>
                <Text style={styles.sectionSubtitle}>Size - Threads Per Inch</Text>
                <ScrollView 
                  contentContainerStyle={styles.scrollViewContent}
                  style={styles.scrollView}
                  showsVerticalScrollIndicator={false}
                >
                  {sizes.map((size, index) => (
                    <SizeButton
                        key={index}
                        title={size.size}
                        price={size.basePrice}
                        onPress={() => handleSizeSelection(size.size)}
                        isSelected={selectedSize === size.size}
                    />
                  ))}
  
                  {/* Add Ferrule image and text */}
                  <>
                    <View style={styles.ferruleContainer}>
                      <Image
                        source={require('../assets/Ferrule.PNG')}
                        style={styles.ferruleImage}
                        resizeMode="contain"
                      />
                    </View>
                    <View style={styles.ferruleTextContainer}>
                      <Text style={styles.ferruleText}>
                        Price Inclusive of Ferrule
                      </Text>
                    </View>
                  </>
                </ScrollView>
              </>
            ) : (
              <View style={styles.placeholderColumn} />
            )}
          </View>
        </View>
        <View style={styles.fittingTypeContainer}>
          <Text style={styles.fittingTypeText}>JIC 37 (Joint Industrial Council)</Text>
        </View>
      </Animated.View>
      </SafeAreaView>
    </ScrollableGradientWrapper>
    </Animated.View>
);
};

