import React, { useEffect, useState, useRef, useCallback } from 'react';
import { 
  View, 
  Text,
  Image, 
  StyleSheet, 
  SafeAreaView, 
  ScrollView, 
  TouchableOpacity, 
  BackHandler,
  Dimensions,
  Animated,
  Platform,
  Linking
} from 'react-native';
import { useNavigation } from '@react-navigation/native';
import ScrollableGradientWrapper from '../components/ScrollableGradientWrapper';
import PDFGenerator from '../components/PDFGenerator';
import { sendOrderEmail } from '../utils/emailService';

const { width: SCREEN_WIDTH } = Dimensions.get('window');
const BUTTON_WIDTH = Math.min(320, SCREEN_WIDTH * 0.8);

// Helper Components
const LabelItem = ({ children }) => (
  <View style={styles.labelItem}>
    <Text style={styles.labelText}>{children}</Text>
  </View>
);

const BorderedItem = ({ children, price, style, isCutLengths }) => (
  <View style={[styles.borderedItem, style]}>
    {isCutLengths ? (
      <View style={styles.cutLengthsLayout}>
        <View style={styles.cutLengthsContent}>
          {children}
        </View>
        {price && (
          <View style={styles.priceBorder}>
            <Text style={styles.priceText}>
              {price === 'FREE' || price === 'NOT REQUIRED' 
                ? price 
                : typeof price === 'number' 
                  ? `A$${price.toFixed(2)}` 
                  : `A$${parseFloat(price).toFixed(2)}`}
            </Text>
          </View>
        )}
      </View>
    ) : (
      <>
        <Text style={styles.itemText}>{children}</Text>
        {price && (
          <View style={styles.priceBorder}>
            <Text style={styles.priceText}>
              {price === 'FREE' || price === 'NOT REQUIRED' 
                ? price 
                : typeof price === 'number' 
                  ? `A$${price.toFixed(2)}` 
                  : `A$${parseFloat(price).toFixed(2)}`}
            </Text>
          </View>
        )}
      </>
    )}
  </View>
);

const OrderConfirmationScreen = ({ route }) => {
  const navigation = useNavigation();
  const { success, orderDetails, error } = route.params;
  const fadeAnim = React.useRef(new Animated.Value(1)).current;
  const contentRef = useRef(null);
  const [isGeneratingPDF, setIsGeneratingPDF] = useState(false);
  const orderId = orderDetails?.paymentId || 'Unknown';
  const [emailSent, setEmailSent] = useState(false);
  const [selectedButton, setSelectedButton] = useState(null);

  const {
    userDetails,
    selectedHose,
    selectedHosePrice,
    selectedFitting,
    end1Shape,
    end1Size,
    end1Price,
    selectedEnd2Fitting,
    end2Shape,
    end2Size,
    end2Price,
    selectedAngle,
    quantity,
    cutLengths,
    cutLengthPrice, isOrderFittingMode,
    selectedProtection,
    hoseProtectionPrice,
    selectedPressure,
    pressureTestPrice,
    selectedMethod,
    selectedService,
    shippingCost,
    totalBeforeShipping,
    totalPriceWithShipping,
    protectionCost,
    paymentId,
    amount
  } = orderDetails || {};

  const calculatePriceBreakdown = (details) => {
    if (details.isOrderFittingMode) {
      const subtotal = details.cutLengthPrice || 0;
      const shipping = 10; // Fixed $10 shipping for Fitting Mode
      const subtotalWithShipping = subtotal + shipping;
      const gst = subtotalWithShipping * 0.1; // 10% GST
      const total = subtotalWithShipping + gst;
      
      return {
        subtotal,
        shipping,
        subtotalWithShipping,
        gst,
        total
      };
    } else {
      const subtotal = details.totalBeforeShipping || 0;
      const shipping = details.shippingCost || 0;
      const subtotalWithShipping = subtotal + shipping;
      const gst = subtotalWithShipping * 0.1; // 10% GST
      const total = subtotalWithShipping + gst;
      
      return {
        subtotal,
        shipping,
        subtotalWithShipping,
        gst,
        total
      };
    }
  };

  // Calculate price breakdown once for the component
  const priceBreakdown = calculatePriceBreakdown(orderDetails);

  useEffect(() => {
    const backHandler = BackHandler.addEventListener('hardwareBackPress', () => {
      handlePlaceAnotherOrder();
      return true;
    });
    return () => backHandler.remove();
  }, [navigation]);

  const handlePlaceAnotherOrder = () => {
    navigation.reset({
      index: 0,
      routes: [{ name: 'Welcome' }],
    });
  };

  // Auto-generate PDF when screen loads
  useEffect(() => {
    const autoGeneratePDF = async () => {
      if (success && contentRef.current && !emailSent) {
        // Wait a brief moment for content to render
        setTimeout(async () => {
          await generatePDF({ silent: true }); // Generate PDF without triggering download
        }, 1000);
      }
    };

    autoGeneratePDF();
  }, [success, emailSent]);

  const { generatePDF } = PDFGenerator({
    contentRef,
    orderId: orderDetails?.paymentId,
    onGenerateStart: () => {
      console.log('Starting PDF generation...');
      setIsGeneratingPDF(true);
    },
    onGenerateComplete: async (pdfBuffer) => {
      console.log('PDF generation completed');
      setIsGeneratingPDF(false);
      
      // If this was an automatic generation and email hasn't been sent yet
      if (pdfBuffer && !emailSent && success) {
        console.log('Attempting to send email...');
        try {
          // Prepare the email data
          const emailData = {
            ...orderDetails,
            pdfAttachment: pdfBuffer,
            userEmail: userDetails?.email || 'absareta@gmail.com', // Use customer email if available
            orderNumber: paymentId || 'Unknown',
            totalAmount: totalBeforeShipping * 1.1 // Including GST
          };
          
          await sendOrderEmail(emailData);
          setEmailSent(true);
          console.log('Order confirmation email sent successfully');
        } catch (error) {
          console.error('Failed to send order confirmation email:', error);
        }
      }
    },
    onError: (error) => {
      console.error('PDF generation failed:', error);
      setIsGeneratingPDF(false);
    }
  });

  const handlePDFDownload = async () => {
    if (isGeneratingPDF) return;
    await generatePDF();
  };

  const renderShippingDetails = () => (
    <View style={styles.detailsBox}>
      <View style={styles.boxHeader}>
        <Text style={styles.boxHeaderText}>Shipping Details</Text>
      </View>
      
      <View style={styles.detailsGrid}>
        <View style={styles.detailRow}>
          <Text style={styles.detailLabel}>Name:</Text>
          <Text style={styles.detailValue}>{userDetails?.name}</Text>
        </View>
        
        {userDetails?.companyName && (
          <View style={styles.detailRow}>
            <Text style={styles.detailLabel}>Company:</Text>
            <Text style={styles.detailValue}>{userDetails.companyName}</Text>
          </View>
        )}
        
        <View style={styles.detailRow}>
          <Text style={styles.detailLabel}>Address:</Text>
          <Text style={styles.detailValue}>{userDetails?.address}</Text>
        </View>
        
        <View style={styles.detailRow}>
          <Text style={styles.detailLabel}>Suburb:</Text>
          <Text style={styles.detailValue}>{userDetails?.suburb}</Text>
        </View>
        
        <View style={styles.detailRow}>
          <Text style={styles.detailLabel}>State:</Text>
          <Text style={styles.detailValue}>{userDetails?.state}</Text>
        </View>
        
        <View style={styles.detailRow}>
          <Text style={styles.detailLabel}>Postcode:</Text>
          <Text style={styles.detailValue}>{userDetails?.postcode}</Text>
        </View>

        <View style={styles.detailRow}>
          <Text style={styles.detailLabel}>Email:</Text>
          <Text style={styles.detailValue}>{userDetails?.email}</Text>
        </View>
        
        {userDetails?.contactNumber && (
          <View style={styles.detailRow}>
            <Text style={styles.detailLabel}>Contact:</Text>
            <Text style={styles.detailValue}>{userDetails.contactNumber}</Text>
          </View>
        )}
      </View>
    </View>
  );

  const renderOrderSummary = () => (
    <View style={styles.detailsBox}>
      <View style={styles.boxHeader}>
        <Text style={styles.boxHeaderText}>Order Summary</Text>
      </View>
      
      <View style={styles.orderDetailsContainer}>
        {paymentId && (
          <View style={styles.rowContainer}>
            <LabelItem>ORDER ID</LabelItem>
            <BorderedItem>{paymentId}</BorderedItem>
          </View>
        )}
        
        {selectedHose && (
          <View style={styles.rowContainer}>
            <LabelItem>HOSE SIZE</LabelItem>
            <BorderedItem>
              {typeof selectedHose === 'object' && selectedHose.size ? selectedHose.size : selectedHose}
            </BorderedItem>
          </View>
        )}
        
        {end1Shape && end1Size && (
          <View style={styles.rowContainer}>
            <LabelItem>END1</LabelItem>
            <BorderedItem price={end1Price}>
              {`${end1Shape} - ${end1Size}`}
            </BorderedItem>
          </View>
        )}
        
        {end2Shape && end2Size && (
          <View style={styles.rowContainer}>
            <LabelItem>END2</LabelItem>
            <BorderedItem price={end2Price}>
              {`${end2Shape} - ${end2Size}`}
            </BorderedItem>
          </View>
        )}
        
        {selectedAngle && (
          <View style={styles.rowContainer}>
            <LabelItem>ANGLE</LabelItem>
            <BorderedItem>{selectedAngle}</BorderedItem>
          </View>
        )}
        
        {quantity && (
          <View style={styles.rowContainer}>
            <LabelItem>QUANTITY</LabelItem>
            <BorderedItem>{quantity}</BorderedItem>
          </View>
        )}
        
        {cutLengths && cutLengths.length > 0 && (
          <View style={styles.rowContainer}>
            <LabelItem>CUT LENGTHS</LabelItem>
            <BorderedItem 
              price={cutLengths.reduce((total, cut) => {
                const lengthInMeters = parseFloat(cut.length) / 1000;
                const basePrice = parseFloat(selectedHosePrice);
                return total + (lengthInMeters * basePrice);
              }, 0).toFixed(2)}
              isCutLengths
            >
              {cutLengths.map((cut, index) => (
                <View key={index} style={styles.cutLengthRow}>
                  <View style={styles.numberedCircle}>
                    <Text style={styles.numberedCircleText}>{index + 1}</Text>
                  </View>
                  <Text style={styles.cutLengthText}>
                    {`${cut.length} ${cut.unit}`}
                  </Text>
                </View>
              ))}
            </BorderedItem>
          </View>
        )}
        
        {selectedProtection && (
          <View style={styles.rowContainer}>
            <LabelItem>PROTECTION</LabelItem>
            <BorderedItem price={selectedProtection === 'NOT REQUIRED' ? 'NOT REQUIRED' : protectionCost}>
              {selectedProtection}
            </BorderedItem>
          </View>
        )}
        
        {selectedPressure && (
          <View style={styles.rowContainer}>
            <LabelItem>PRESSURE TEST</LabelItem>
            <BorderedItem price={selectedPressure === 'Not Required' ? 'NOT REQUIRED' : pressureTestPrice}>
              {selectedPressure}
            </BorderedItem>
          </View>
        )}
        
        {(selectedService || isOrderFittingMode) && (
          <View style={styles.rowContainer}>
            <LabelItem>SHIPPING COST</LabelItem>
            <BorderedItem price={isOrderFittingMode ? 10 : (shippingCost || 'FREE')}>
              {isOrderFittingMode ? 'Standard Shipping' : selectedService || 'Standard Shipping'}
            </BorderedItem>
          </View>
        )}
        
        {/* Add this new price breakdown section 
          <View style={styles.rowContainer}>
            <LabelItem>SUBTOTAL</LabelItem>
            <BorderedItem price={priceBreakdown.subtotal}>
              Items Total
            </BorderedItem>
          </View>

          <View style={styles.rowContainer}>
            <LabelItem>SHIPPING</LabelItem>
            <BorderedItem price={priceBreakdown.shipping}>
              {isOrderFittingMode ? 'Standard Shipping' : (selectedService || 'Standard Shipping')}
            </BorderedItem>
          </View>*/}

          <View style={styles.rowContainer}>
            <LabelItem>SUBTOTAL WITH SHIPPING</LabelItem>
            <BorderedItem price={priceBreakdown.subtotalWithShipping}>
            </BorderedItem>
          </View>

          <View style={styles.rowContainer}>
            <LabelItem>GST (10%)</LabelItem>
            <BorderedItem price={priceBreakdown.gst}>
            </BorderedItem>
          </View>

          <View style={styles.totalContainer}>
            <Text style={styles.totalLabel}>Total Amount (Inc. GST)</Text>
            <Text style={styles.totalValue}>
              A${priceBreakdown.total.toFixed(2)}
            </Text>
          </View>
              </View>
              </View>
  );

  if (!success) {
    return (
      <SafeAreaView style={styles.container}>
        <View style={styles.logoContainer}>
          <Image
            source={require('../assets/fluidpower_logo.gif')}
            style={styles.logo}
            resizeMode="contain"
          />
        </View>
        
        <View style={styles.errorContainer}>
          <View style={styles.titleWrapper}>
            <Text style={styles.titleText}> Payment Failed</Text>
          </View>
          <Text style={[styles.errorText, { marginTop: 20 }]}>
            {error || 'Payment processing failed. Please try again.'}
          </Text>
          
          <View style={styles.buttonContainer}>
            <TouchableOpacity 
              style={[
                styles.actionButton,
                { backgroundColor: selectedButton === 'tryAgain' ? '#ffc100' : 'transparent' }
              ]}
              onPress={() => {
                setSelectedButton('tryAgain');
                setTimeout(() => {
                  // Navigate to Payment screen with all the necessary values
                  navigation.navigate('Payment', {
                    selectedHose,
                    selectedHosePrice,
                    selectedFitting,
                    end1Shape,
                    end1Size,
                    end1Price,
                    selectedEnd2Fitting,
                    end2Shape,
                    end2Size,
                    end2Price,
                    selectedAngle,
                    quantity,
                    cutLengths,
                    cutLengthPrice,
                    isOrderFittingMode,
                    selectedProtection,
                    hoseProtectionPrice,
                    selectedPressure,
                    pressureTestPrice,
                    selectedMethod,
                    selectedService,
                    shippingCost,
                    totalBeforeShipping,
                    totalPriceWithShipping,
                    protectionCost,
                    shippingDetails: orderDetails.userDetails // Pass the shipping details
                  });
                }, 100);
              }}
              activeOpacity={0.8}
            >
              <Text style={styles.buttonText}>Try Again</Text>
            </TouchableOpacity>
  
            <TouchableOpacity 
              style={[
                styles.actionButton,
                { backgroundColor: selectedButton === 'newOrder' ? '#ffc100' : 'transparent' }
              ]}
              onPress={() => {
                setSelectedButton('newOrder');
                setTimeout(handlePlaceAnotherOrder, 100);
              }}
              activeOpacity={0.8}
            >
              <Text style={styles.buttonText}>Place New Order</Text>
            </TouchableOpacity>
          </View>
        </View>
      </SafeAreaView>
    );
  }

  const handlePhonePress = () => {
    Linking.openURL('tel:+61409517333');
  };
  
  const handleEmailPress = () => {
    Linking.openURL('mailto:info@fluidpowergroup.com.au');
  };

  return (
    <View style={styles.mainContainer}>
      {/* Floating Buttons */}
      <TouchableOpacity 
        style={styles.floatingBackButton}
        onPress={handlePlaceAnotherOrder}
      >
        <Text style={styles.backButtonText}>Place New Order</Text>
      </TouchableOpacity>
  
      <TouchableOpacity 
        style={[
          styles.floatingDownloadButton,
          isGeneratingPDF && styles.disabledButton
        ]}
        onPress={handlePDFDownload}
        disabled={isGeneratingPDF}
      >
        <Text style={styles.backButtonText}>
          {isGeneratingPDF ? 'Generating PDF...' : 'Download PDF'}
        </Text>
      </TouchableOpacity>
  
      <ScrollableGradientWrapper>
        {Platform.OS === 'web' ? (
          <>
            {/* Screen View - This won't be included in PDF */}
            <SafeAreaView style={styles.container}>
              <View style={styles.logoContainer}>
                <Image
                  source={require('../assets/fluidpower_logo.gif')}
                  style={styles.logo}
                  resizeMode="contain"
                />
              </View>
  
              <Animated.View style={[styles.contentContainer, { opacity: fadeAnim }]}>
                <View style={styles.titleContainer}>
                  <View style={styles.titleWrapper}>
                    <Text style={styles.titleText}>
                      {success ? ' Payment Successful!' : ' Payment Failed'}
                    </Text>
                  </View>
                </View>
  
                <Text style={styles.successTitle}>Thank You for Your Order!</Text>
                <Text style={styles.emailTitle}>Don't forget to check Junk/Spam folder of your email for Confirmation</Text>
  
                <ScrollView 
                  style={styles.mainScrollView}
                  contentContainerStyle={styles.mainScrollViewContent}
                  showsVerticalScrollIndicator={true}
                >
                  {success ? (
                    <>
                      {renderOrderSummary()}
                      {renderShippingDetails()}
                    </>
                  ) : (
                    <View style={styles.errorContainer}>
                      {/* Error content */}
                    </View>
                  )}
                </ScrollView>

                {/* Add this View container here */}
                <View style={styles.copyrightContainer}>
                  <Text style={styles.enquiryText}>
                    For any and all enquiries:
                  </Text>
                  
                  <TouchableOpacity 
                    onPress={handlePhonePress}
                    style={styles.touchableArea}
                  >
                    <Text style={styles.contactText}>
                      +61 409 517 333
                    </Text>
                  </TouchableOpacity>
                  
                  <TouchableOpacity 
                    onPress={handleEmailPress}
                    style={styles.touchableArea}
                  >
                    <Text style={styles.contactText}>
                      email: info@fluidpowergroup.com.au
                    </Text>
                  </TouchableOpacity>

                  <Text style={styles.copyrightText}>
                    FluidPower Group All Rights Reserved {new Date().getFullYear()}
                  </Text>
                </View>
              </Animated.View>
            </SafeAreaView>
  
            {/* PDF View - This div and its contents will be used for PDF generation */}
            <div 
                ref={contentRef} 
                style={{ 
                  position: 'absolute',
                  left: '-9999px',
                  top: 0,
                  width: '800px',  // Fixed width for PDF generation
                  backgroundColor: 'white'
                }}
              >
                <SafeAreaView style={[styles.container, { width: '100%' }]}>
                  <View style={[styles.logoContainer, { width: '100%', height: 'auto' }]}>
                    <img
                      src={require('../assets/fluidpower_logo.gif')}
                      style={{
                        width: '400px',
                        height: '200px',
                        objectFit: 'contain'
                      }}
                      alt="FluidPower Logo"
                    />
                  </View>

                  <View style={styles.contentContainer}>
                    <ScrollView 
                      style={styles.mainScrollView}
                      contentContainerStyle={styles.mainScrollViewContent}
                    >
                      {success && (
                        <>
                          {renderOrderSummary()}
                          {renderShippingDetails()}
                        </>
                      )}
                    </ScrollView>
                  </View>

                  <View style={styles.copyrightContainer}>
                    <Text style={styles.enquiryText}>
                      For any and all enquiries:
                    </Text>
                    
                    <TouchableOpacity 
                      onPress={handlePhonePress}
                      style={styles.touchableArea}
                    >
                      <Text style={styles.contactText}>
                        +61 409 517 333
                      </Text>
                    </TouchableOpacity>
                    
                    <TouchableOpacity 
                      onPress={handleEmailPress}
                      style={styles.touchableArea}
                    >
                      <Text style={styles.contactText}>
                        email: info@fluidpowergroup.com.au
                      </Text>
                    </TouchableOpacity>

                    <Text style={styles.copyrightText}>
                      FluidPower Group All Rights Reserved {new Date().getFullYear()}
                    </Text>
                  </View>
                </SafeAreaView>
              </div>
          </>
        ) : (
          <ViewShot
            ref={contentRef}
            options={{
              format: 'jpg',
              quality: 0.9,
            }}
          >
            <SafeAreaView style={styles.container}>
              {/* Your existing content for non-web platforms */}
            </SafeAreaView>
          </ViewShot>
        )}
      </ScrollableGradientWrapper>
    </View>
  );
};

