// FittingSelectionScreen.js
import React, { useState } from 'react';
import { View, Text, StyleSheet, TouchableOpacity, ScrollView } from 'react-native';
import { useNavigation } from '@react-navigation/native';

const FittingSelectionScreen = ({ route }) => {
  const navigation = useNavigation();
  const { selectedHose } = route.params;
  const [selectedFitting, setSelectedFitting] = useState(null);

  const fittings = [
    { type: 'JIC 37', shapes: ['Male Straight', 'Female Straight', '45 Female Elbow', '90 Female Elbow'] },
    { type: 'BSP 60', shapes: ['Male Straight', 'Female Straight', '45 Female Elbow', '90 Female Elbow'] },
    { type: 'Metric Light 24', shapes: ['Male Straight', 'Female Straight', '45 Female Elbow', '90 Female Elbow'] },
    { type: 'O.R.F.S', shapes: ['Male Straight', 'Female Straight', '45 Female Elbow', '90 Female Elbow'] },
  ];

  const handleSelection = (fitting) => {
    setSelectedFitting(fitting);
    // Add animation logic here if desired
    setTimeout(() => {
      navigation.navigate('Summary', { selectedHose, selectedFitting });
    }, 1000);
  };

  return (
    <ScrollView contentContainerStyle={styles.container}>
      <Text style={styles.title}>Select Fitting Type</Text>
      <Text style={styles.subtitle}>For Hose Size: {selectedHose.size}</Text>
      {fittings.map((fitting) => (
        <View key={fitting.type}>
          <Text style={styles.fittingType}>{fitting.type}</Text>
          {fitting.shapes.map((shape) => (
            <TouchableOpacity
              key={shape}
              style={[styles.fittingOption, selectedFitting === `${fitting.type} - ${shape}` && styles.selectedOption]}
              onPress={() => handleSelection(`${fitting.type} - ${shape}`)}
            >
              <Text style={styles.fittingShape}>{shape}</Text>
            </TouchableOpacity>
          ))}
        </View>
      ))}
    </ScrollView>
  );
};

const styles = StyleSheet.create({
  container: {
    flexGrow: 1,
    padding: 20,
    backgroundColor: '#fff',
  },
  title: {
    fontSize: 24,
    fontWeight: 'bold',
    marginBottom: 10,
    textAlign: 'center',
  },
  subtitle: {
    fontSize: 18,
    marginBottom: 20,
    textAlign: 'center',
  },
  fittingType: {
    fontSize: 20,
    fontWeight: 'bold',
    marginTop: 20,
    marginBottom: 10,
  },
  fittingOption: {
    borderWidth: 1,
    borderColor: '#FFA500',
    borderRadius: 10,
    padding: 15,
    marginBottom: 10,
  },
  selectedOption: {
    backgroundColor: '#FFF7E6',
  },
  fittingShape: {
    fontSize: 16,
  },
});

export default FittingSelectionScreen;