import React, { useState, useRef, useEffect } from 'react';
import { View, Text, Modal, StyleSheet, TouchableOpacity, TouchableWithoutFeedback, ScrollView, SafeAreaView, Image, Dimensions, Platform, Animated } from 'react-native';
import { useNavigation, useFocusEffect } from '@react-navigation/native';
import ScrollableGradientWrapper from '../components/ScrollableGradientWrapper';

const { width: SCREEN_WIDTH } = Dimensions.get('window');

const AngleButton = ({ angle, image, onPress, isSelected, onHelpPress }) => (
  <TouchableOpacity 
    style={[styles.angleButton, isSelected && styles.selectedAngleButton]} 
    onPress={onPress}
  >
    <Image source={image} style={styles.angleImage} resizeMode="contain" />
    <Text style={styles.angleText}>{angle}</Text>
    <TouchableOpacity 
      style={styles.helpIcon} 
      onPress={onHelpPress}
      hitSlop={{ top: 10, right: 10, bottom: 10, left: 10 }} // Additional hit area
    >
      <View style={styles.helpIconVisual}>
        <Text style={styles.helpIconText}>?</Text>
      </View>
    </TouchableOpacity>
  </TouchableOpacity>
);

const NotSureButton = ({ onPress, isSelected }) => (
  <TouchableOpacity 
    style={[
      styles.notSureButton, 
      isSelected && styles.selectedAngleButton
    ]} 
    onPress={onPress}
  >
    <Text style={styles.notSureText}>NOT SURE</Text>
    <Text style={styles.notSureSubtext1}>Click Here if you're Unsure and</Text>
    <Text style={styles.notSureSubtext2}>we'll contact you to understand your Requirements</Text>
  </TouchableOpacity>
);

const OrientationSelectionScreen = ({ route }) => { 
  const navigation = useNavigation();
  const [selectedAngle, setSelectedAngle] = useState(null);
  const { selectedHose, selectedHosePrice,  selectedFitting, end1Shape, end1Size, end1Price, selectedEnd2Fitting, end2Shape, end2Size, end2Price  } = route.params;
  const fadeAnim = useRef(new Animated.Value(0)).current;
  const [showHelpOverlay, setShowHelpOverlay] = useState(false);
  const [currentHelpImage, setCurrentHelpImage] = useState(null);
  const helpFadeAnim = useRef(new Animated.Value(0)).current;
  const [showDiagramOverlay, setShowDiagramOverlay] = useState(false);
  const diagramFadeAnim = useRef(new Animated.Value(0)).current;

  const angles = [
    { angle: '0', image: require('../assets/0_degrees.png'), helpImage: require('../assets/0_degrees_help.png') },
    { angle: '45', image: require('../assets/45_degrees.png'), helpImage: require('../assets/45_degrees_help.png') },
    { angle: '90', image: require('../assets/90_degrees.png'), helpImage: require('../assets/90_degrees_help.png') },
    { angle: '135', image: require('../assets/135_degrees.png'), helpImage: require('../assets/135_degrees_help.png') },
    { angle: '180', image: require('../assets/180_degrees.png'), helpImage: require('../assets/180_degrees_help.png') },
    { angle: '225', image: require('../assets/225_degrees.png'), helpImage: require('../assets/225_degrees_help.png') },
    { angle: '270', image: require('../assets/270_degrees.png'), helpImage: require('../assets/270_degrees_help.png') },
    { angle: '315', image: require('../assets/315_degrees.png'), helpImage: require('../assets/315_degrees_help.png') },
    { angle: 'NOT SURE', image: null }
  ];



  const handleAngleSelection = (angle) => {
    setSelectedAngle(angle);
    // Start fade-out animation
    Animated.timing(fadeAnim, {
      toValue: 0,
      duration: 800,
      useNativeDriver: Platform.OS === 'native',
    }).start(() => {
      // Navigate to next screen after fade-out completes
      navigation.navigate('CutLengths', { 
        selectedHose,
        selectedHosePrice, 
        selectedFitting,
        end1Shape, 
        end1Size,
        end1Price,
        selectedEnd2Fitting,
        end2Shape,
        end2Size,
        end2Price,
        selectedAngle: angle
      });
    });
  };

  const handleHelpPress = (helpImage) => {
    setCurrentHelpImage(helpImage);
    setShowHelpOverlay(true);
    helpFadeAnim.setValue(0);
    Animated.timing(helpFadeAnim, {
      toValue: 1,
      duration: 500,
      useNativeDriver: Platform.OS === 'native',
    }).start();
  };
  
  const closeHelpOverlay = () => {
    Animated.timing(helpFadeAnim, {
      toValue: 0,
      duration: 300,
      useNativeDriver: Platform.OS === 'native',
    }).start(() => {
      setShowHelpOverlay(false);
    });
  };

  const handleDiagramPress = () => {
    setShowDiagramOverlay(true);
    diagramFadeAnim.setValue(0);
    Animated.timing(diagramFadeAnim, {
        toValue: 1,
        duration: 500,
        useNativeDriver: Platform.OS === 'native',
    }).start();
  };

  const closeDiagramOverlay = () => {
    Animated.timing(diagramFadeAnim, {
        toValue: 0,
        duration: 300,
        useNativeDriver: Platform.OS === 'native',
    }).start(() => {
        setShowDiagramOverlay(false);
    });
  };

  useEffect(() => {
    // Fade in animation when the component mounts
    Animated.timing(fadeAnim, {
      toValue: 1,
      duration: 800,
      useNativeDriver: Platform.OS === 'native',
    }).start();
  }, []);

  // Reset fade-in when screen comes into focus
  useFocusEffect(
    React.useCallback(() => {
      //setSelectedAngle(null);
      fadeAnim.setValue(0);
      Animated.timing(fadeAnim, {
        toValue: 1,
        duration: 800,
        useNativeDriver: Platform.OS === 'native',
      }).start();

      return () => {
        // Optional cleanup if needed
      };
    }, [fadeAnim])
  );

  return (
    <View style={styles.mainContainer}>
      {/* Floating Back Button */}
      <TouchableOpacity 
        style={styles.floatingBackButton}
        onPress={() => navigation.goBack()}
      >
        <Text style={styles.backButtonText}></Text>
      </TouchableOpacity>
      
      <ScrollableGradientWrapper>
        <SafeAreaView style={styles.container}>
          <View style={styles.logoContainer}>
            <Image
              source={require('../assets/fluidpower_logo.gif')}
              style={styles.logo}
              resizeMode="contain"
            />
          </View>      
          <Animated.View style={[styles.contentContainer, { opacity: fadeAnim }]}>
            <View style={styles.titleContainer}>
              <Text style={styles.titleText}>Select ORIENTATION</Text>
            </View>
            <View style={styles.subtitleContainer}>
              <Text style={styles.subtitleText}>*No Extra Charges*</Text>
            </View>
            <TouchableOpacity onPress={handleDiagramPress}>
                <Image
                    source={require('../assets/line_of_sight.png')}
                    style={styles.diagram}
                    resizeMode="contain"
                />
                <Text style={styles.clickToEnlargeText}>Click photo to enlarge</Text>
            </TouchableOpacity>
            <ScrollView 
                style={styles.scrollView} 
                contentContainerStyle={styles.scrollViewContent}
                centerContent={true}
              >
                <View style={styles.angleButtonsContainer}>
                  {angles.slice(0, 8).map((angleData, index) => (
                    <View key={index} style={styles.angleButtonWrapper}>
                      <AngleButton
                        angle={angleData.angle}
                        image={angleData.image}
                        onPress={() => handleAngleSelection(angleData.angle)}
                        isSelected={selectedAngle === angleData.angle}
                        onHelpPress={() => angleData.helpImage && handleHelpPress(angleData.helpImage)}
                      />
                    </View>
                  ))}
                  <NotSureButton
                    onPress={() => handleAngleSelection('NOT SURE')}
                    isSelected={selectedAngle === 'NOT SURE'}
                  />
                </View>
              </ScrollView>
          </Animated.View>

          <Modal
                transparent={true}
                visible={showDiagramOverlay}
                onRequestClose={closeDiagramOverlay}
            >
                <TouchableWithoutFeedback onPress={closeDiagramOverlay}>
                    <View style={styles.modalOverlay}>
                        <View style={styles.overlayBackground} />
                        <Animated.View style={[styles.diagramImageContainer, { opacity: diagramFadeAnim }]}>
                            <TouchableOpacity 
                                style={styles.closeButton}
                                onPress={closeDiagramOverlay}
                            >
                                <Text style={styles.closeButtonText}></Text>
                            </TouchableOpacity>
                            <Image
                                source={require('../assets/line_of_sight.png')}
                                style={styles.enlargedDiagram}
                                resizeMode="contain"
                            />
                        </Animated.View>
                    </View>
                </TouchableWithoutFeedback>
            </Modal>

            <Modal
              transparent={true}
              visible={showHelpOverlay}
              onRequestClose={closeHelpOverlay}
            >
              <TouchableWithoutFeedback onPress={closeHelpOverlay}>
                <View style={styles.modalOverlay}>
                  <View style={styles.overlayBackground} />
                  <Animated.View style={[styles.helpImageContainer, { opacity: helpFadeAnim }]}>
                    <TouchableOpacity 
                      style={styles.closeButton}
                      onPress={closeHelpOverlay}
                    >
                      <Text style={styles.closeButtonText}></Text>
                    </TouchableOpacity>
                    <Image
                      source={currentHelpImage}
                      style={styles.helpImage}
                      resizeMode="contain"
                    />
                  </Animated.View>
                </View>
              </TouchableWithoutFeedback>
            </Modal>

        </SafeAreaView>
      </ScrollableGradientWrapper>
    </View>
  );
};

